import React, { useState, useCallback, useMemo } from "react";
import {
  X,
  Save,
  Home,
  MapPin,
  DollarSign,
  User,
  FileText,
  Settings,
} from "lucide-react";
import { Button, Input, Select, Modal } from "@/components/ui";
import type {
  CreatePropertyData,
  PropertyType,
  TransactionType,
  PropertyStatus,
} from "@/types";

interface PropertyCreateFormProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (data: CreatePropertyData) => Promise<void>;
  loading?: boolean;
}

const PROPERTY_TYPES: PropertyType[] = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

const TRANSACTION_TYPES: TransactionType[] = [
  "",
  "",
  "",
  "",
];

const PROPERTY_STATUS: PropertyStatus[] = ["", ""];

//    (  - DB integer )
const ROOM_OPTIONS = [
  { value: "1", label: "1" },
  { value: "2", label: "2" },
  { value: "3", label: "3" },
  { value: "4", label: "4" },
  { value: "5", label: "5" },
  { value: "6", label: "6" },
  { value: "7", label: "7" },
];

//    -  Supabase  

export const PropertyCreateForm: React.FC<PropertyCreateFormProps> = ({
  isOpen,
  onClose,
  onSubmit,
  loading = false,
}) => {
  const [formData, setFormData] = useState<CreatePropertyData>({
    title: "",
    type: "",
    transaction_type: "",
    status: "",
    address: "",
    area: 0,
    floor: 1,
    total_floors: 1,
    rooms: 1,
    bathrooms: 1,
    parking: false,
    elevator: false,
  });

  const [errors, setErrors] = useState<
    Partial<Record<keyof CreatePropertyData, string>>
  >({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = useCallback(
    (field: keyof CreatePropertyData, value: any) => {
      setFormData((prev) => ({
        ...prev,
        [field]: value,
      }));

      //  
      if (errors[field]) {
        setErrors((prev) => ({
          ...prev,
          [field]: undefined,
        }));
      }
    },
    [errors],
  );

  //  
  const propertyTypeOptions = useMemo(
    () => PROPERTY_TYPES.map((type) => ({ value: type, label: type })),
    [],
  );

  const transactionTypeOptions = useMemo(
    () => TRANSACTION_TYPES.map((type) => ({ value: type, label: type })),
    [],
  );

  const statusOptions = useMemo(
    () => PROPERTY_STATUS.map((status) => ({ value: status, label: status })),
    [],
  );

  //    

  const validateForm = (): boolean => {
    const newErrors: Partial<Record<keyof CreatePropertyData, string>> = {};

    //  1:  
    if (!formData.title?.trim()) {
      newErrors.title = " ";
    }

    //  2: 
    if (!formData.address?.trim()) {
      newErrors.address = " ";
    }

    //  3:    
    if (
      formData.transaction_type === "" &&
      (!formData.price || formData.price <= 0)
    ) {
      newErrors.price = " ";
    }

    if (
      formData.transaction_type === "" &&
      (!formData.deposit || formData.deposit <= 0)
    ) {
      newErrors.deposit = " ";
    }

    if (formData.transaction_type === "") {
      if (!formData.deposit || formData.deposit <= 0) {
        newErrors.deposit = " ";
      }
      if (!formData.monthly_rent || formData.monthly_rent <= 0) {
        newErrors.monthly_rent = " ";
      }
    }

    //  :    
    if (
      formData.total_floors &&
      formData.floor &&
      formData.total_floors < formData.floor
    ) {
      newErrors.total_floors = "      ";
    }

    //  
    if (!formData.area || formData.area <= 0) {
      newErrors.area = " ";
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    console.log("     ");
    console.log("  :", formData);
    console.log("    ...");

    const isValid = validateForm();
    console.log("    :", isValid);
    console.log("  :", errors);

    if (!isValid) {
      console.log("     -  ");
      return;
    }

    if (isSubmitting) {
      console.log("    -   ");
      return;
    }

    console.log("    ...");
    setIsSubmitting(true);

    try {
      console.log(" onSubmit   ...");
      //    ( )
      if (import.meta.env.DEV) {
        console.log("Form  :", {
          transaction_type: formData.transaction_type,
          price: formData.price,
        });
      }
      await onSubmit(formData);
      console.log("   !");

      //  
      console.log("   ...");
      setFormData({
        title: "",
        type: "",
        transaction_type: "",
        status: "",
        address: "",
        area: 0,
        floor: 1,
        total_floors: 1,
        rooms: 1,
        bathrooms: 1,
        parking: false,
        elevator: false,
      });
      setErrors({});
      console.log("  ...");
      onClose();
    } catch (error) {
      console.error("   :", error);
      console.error("  :", typeof error);
      console.error(
        "  :",
        error instanceof Error
          ? {
              name: error.name,
              message: error.message,
              stack: error.stack,
            }
          : error,
      );

      //    
      alert(
        `  : ${error instanceof Error ? error.message : "   "}`,
      );
    } finally {
      console.log("    ");
      setIsSubmitting(false);
    }
  };

  if (!isOpen) return null;

  return (
    <Modal isOpen={isOpen} onClose={onClose} size="xl">
      <div className="bg-white rounded-2xl shadow-2xl overflow-hidden max-h-[95vh] flex flex-col">
        {/*    */}
        <div className="bg-gradient-to-r from-blue-600 to-indigo-600 px-4 sm:px-6 py-3 sm:py-4 flex-shrink-0">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2 sm:space-x-3 min-w-0 flex-1">
              <div className="p-1.5 sm:p-2 bg-white/20 rounded-lg flex-shrink-0">
                <Home className="w-5 h-5 sm:w-6 sm:h-6 text-white" />
              </div>
              <div className="min-w-0 flex-1">
                <h2 className="text-lg sm:text-xl font-bold text-white truncate">
                    
                </h2>
                <p className="text-blue-100 text-xs sm:text-sm hidden sm:block">
                    
                </p>
              </div>
            </div>
            <Button
              variant="ghost"
              size="sm"
              onClick={onClose}
              className="text-white hover:bg-white/20 active:scale-95 p-2 h-8 w-8 sm:h-10 sm:w-10 rounded-lg flex-shrink-0 ml-2"
            >
              <X size={18} className="sm:w-5 sm:h-5" />
            </Button>
          </div>
        </div>

        <div className="flex-1 overflow-y-auto">
          <form onSubmit={handleSubmit} className="p-3 sm:p-4">
            <div className="space-y-3 sm:space-y-4">
              {/*    */}
              <div className="bg-gradient-to-r from-blue-50 to-indigo-50 rounded-lg p-3 sm:p-4 border border-blue-100">
                <div className="flex items-center space-x-2 mb-3 sm:mb-4">
                  <div className="p-1.5 bg-blue-600 rounded-md">
                    <Settings className="w-4 h-4 text-white" />
                  </div>
                  <div className="min-w-0 flex-1">
                    <h3 className="text-sm sm:text-base font-semibold text-gray-900">
                       
                    </h3>
                    <p className="text-xs text-gray-600 hidden sm:block">
                         
                    </p>
                  </div>
                </div>

                <div className="space-y-3">
                  <div>
                    <Input
                      label=" "
                      value={formData.title}
                      onChange={(e) =>
                        handleInputChange("title", e.target.value)
                      }
                      error={errors.title}
                      placeholder=":    "
                      required
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  </div>

                  <div className="space-y-3 sm:space-y-0 sm:grid sm:grid-cols-3 sm:gap-3">
                    <Select
                      label=" "
                      value={formData.type}
                      onChange={(e) =>
                        handleInputChange(
                          "type",
                          e.target.value as PropertyType,
                        )
                      }
                      options={propertyTypeOptions}
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />

                    <Select
                      label=" "
                      value={formData.transaction_type}
                      onChange={(e) =>
                        handleInputChange(
                          "transaction_type",
                          e.target.value as TransactionType,
                        )
                      }
                      options={transactionTypeOptions}
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />

                    <Select
                      label=" "
                      value={formData.status}
                      onChange={(e) =>
                        handleInputChange(
                          "status",
                          e.target.value as PropertyStatus,
                        )
                      }
                      options={statusOptions}
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  </div>
                </div>
              </div>

              {/*    */}
              <div className="bg-gradient-to-r from-green-50 to-emerald-50 rounded-lg p-3 sm:p-4 border border-green-100">
                <div className="flex items-center space-x-2 mb-3 sm:mb-4">
                  <div className="p-1.5 bg-green-600 rounded-md">
                    <MapPin className="w-4 h-4 text-white" />
                  </div>
                  <div className="min-w-0 flex-1">
                    <h3 className="text-sm sm:text-base font-semibold text-gray-900">
                       
                    </h3>
                    <p className="text-xs text-gray-600 hidden sm:block">
                         
                    </p>
                  </div>
                </div>

                <div className="space-y-3">
                  <div>
                    <Input
                      label=""
                      value={formData.address}
                      onChange={(e) =>
                        handleInputChange("address", e.target.value)
                      }
                      error={errors.address}
                      placeholder=":    123-45"
                      required
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  </div>

                  <div>
                    <Input
                      label=" "
                      value={formData.detailed_address || ""}
                      onChange={(e) =>
                        handleInputChange("detailed_address", e.target.value)
                      }
                      placeholder=": 123 456"
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  </div>
                </div>
              </div>

              {/*    */}
              <div className="bg-gradient-to-r from-purple-50 to-violet-50 rounded-lg p-3 sm:p-4 border border-purple-100">
                <div className="flex items-center space-x-2 mb-3 sm:mb-4">
                  <div className="p-1.5 bg-purple-600 rounded-md">
                    <Home className="w-4 h-4 text-white" />
                  </div>
                  <div className="min-w-0 flex-1">
                    <h3 className="text-sm sm:text-base font-semibold text-gray-900">
                       
                    </h3>
                    <p className="text-xs text-gray-600 hidden sm:block">
                         
                    </p>
                  </div>
                </div>

                <div className="space-y-4">
                  {/*    */}
                  <div className="space-y-3 sm:space-y-0 sm:grid sm:grid-cols-2 md:grid-cols-3 sm:gap-3">
                    <Input
                      label=" (m)"
                      type="number"
                      value={formData.area || ""}
                      onChange={(e) =>
                        handleInputChange(
                          "area",
                          parseFloat(e.target.value) || 0,
                        )
                      }
                      error={errors.area}
                      placeholder="85.0"
                      min="0"
                      step="0.1"
                      required
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />

                    <Input
                      label=""
                      type="number"
                      value={formData.floor || ""}
                      onChange={(e) =>
                        handleInputChange(
                          "floor",
                          parseInt(e.target.value) || 1,
                        )
                      }
                      error={errors.floor}
                      placeholder="15"
                      min="1"
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />

                    <Input
                      label=" "
                      type="number"
                      value={formData.total_floors || ""}
                      onChange={(e) =>
                        handleInputChange(
                          "total_floors",
                          parseInt(e.target.value) || 1,
                        )
                      }
                      error={errors.total_floors}
                      placeholder="25"
                      min="1"
                      className="text-sm sm:text-base h-11 sm:h-12 sm:col-span-2 md:col-span-1"
                    />
                  </div>

                  {/*   */}
                  <div className="space-y-3 sm:space-y-0 sm:grid sm:grid-cols-2 sm:gap-3">
                    <Select
                      label=" "
                      value={formData.rooms?.toString() || "1"}
                      onChange={(e) =>
                        handleInputChange(
                          "rooms",
                          parseInt(e.target.value) || 1,
                        )
                      }
                      error={errors.rooms}
                      options={ROOM_OPTIONS}
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />

                    <Input
                      label=" "
                      type="number"
                      value={formData.bathrooms || ""}
                      onChange={(e) =>
                        handleInputChange(
                          "bathrooms",
                          parseInt(e.target.value) || 1,
                        )
                      }
                      error={errors.bathrooms}
                      placeholder="2"
                      min="1"
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  </div>

                  {/*  */}
                  <div className="bg-white/60 rounded-lg p-3 border border-purple-200">
                    <h4 className="text-xs sm:text-sm font-semibold text-gray-900 mb-2">
                      
                    </h4>
                    <div className="grid grid-cols-2 gap-2 sm:flex sm:flex-wrap sm:gap-3">
                      <label className="flex items-center space-x-2 bg-white rounded-md px-2 sm:px-3 py-2 border border-gray-200 hover:border-purple-300 active:scale-95 transition-all cursor-pointer">
                        <input
                          type="checkbox"
                          checked={formData.parking}
                          onChange={(e) =>
                            handleInputChange("parking", e.target.checked)
                          }
                          className="rounded border-gray-300 text-purple-600 focus:ring-purple-500 w-4 h-4 sm:w-3 sm:h-3"
                        />
                        <span className="text-xs font-medium text-gray-700">
                           
                        </span>
                      </label>

                      <label className="flex items-center space-x-2 bg-white rounded-md px-2 sm:px-3 py-2 border border-gray-200 hover:border-purple-300 active:scale-95 transition-all cursor-pointer">
                        <input
                          type="checkbox"
                          checked={formData.elevator}
                          onChange={(e) =>
                            handleInputChange("elevator", e.target.checked)
                          }
                          className="rounded border-gray-300 text-purple-600 focus:ring-purple-500 w-4 h-4 sm:w-3 sm:h-3"
                        />
                        <span className="text-xs font-medium text-gray-700">
                           
                        </span>
                      </label>
                    </div>
                  </div>
                </div>
              </div>

              {/*    */}
              <div className="bg-gradient-to-r from-orange-50 to-amber-50 rounded-lg p-3 sm:p-4 border border-orange-100">
                <div className="flex items-center space-x-2 mb-3 sm:mb-4">
                  <div className="p-1.5 bg-orange-600 rounded-md">
                    <DollarSign className="w-4 h-4 text-white" />
                  </div>
                  <div className="min-w-0 flex-1">
                    <h3 className="text-sm sm:text-base font-semibold text-gray-900">
                       
                    </h3>
                    <p className="text-xs text-gray-600 hidden sm:block">
                          
                    </p>
                  </div>
                </div>

                <div className="space-y-3 sm:space-y-0 sm:grid sm:grid-cols-1 md:grid-cols-3 sm:gap-3">
                  {formData.transaction_type === "" && (
                    <Input
                      label=" ()"
                      type="number"
                      value={formData.price || ""}
                      onChange={(e) =>
                        handleInputChange(
                          "price",
                          parseInt(e.target.value) || undefined,
                        )
                      }
                      error={errors.price}
                      placeholder="35000"
                      min="0"
                      required
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  )}

                  {(formData.transaction_type === "" ||
                    formData.transaction_type === "") && (
                    <Input
                      label={
                        formData.transaction_type === ""
                          ? " ()"
                          : " ()"
                      }
                      type="number"
                      value={formData.deposit || ""}
                      onChange={(e) =>
                        handleInputChange(
                          "deposit",
                          parseInt(e.target.value) || undefined,
                        )
                      }
                      error={errors.deposit}
                      placeholder="21000"
                      min="0"
                      required
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  )}

                  {formData.transaction_type === "" && (
                    <Input
                      label=" ()"
                      type="number"
                      value={formData.monthly_rent || ""}
                      onChange={(e) =>
                        handleInputChange(
                          "monthly_rent",
                          parseInt(e.target.value) || undefined,
                        )
                      }
                      error={errors.monthly_rent}
                      placeholder="65"
                      min="0"
                      required
                      className="text-sm sm:text-base h-11 sm:h-12"
                    />
                  )}
                </div>
              </div>

              {/*    */}
              <div className="bg-gradient-to-r from-rose-50 to-pink-50 rounded-lg p-3 sm:p-4 border border-rose-100">
                <div className="flex items-center space-x-2 mb-3 sm:mb-4">
                  <div className="p-1.5 bg-rose-600 rounded-md">
                    <User className="w-4 h-4 text-white" />
                  </div>
                  <div className="min-w-0 flex-1">
                    <h3 className="text-sm sm:text-base font-semibold text-gray-900">
                       
                    </h3>
                    <p className="text-xs text-gray-600 hidden sm:block">
                         
                    </p>
                  </div>
                </div>

                <div className="space-y-3 sm:space-y-0 sm:grid sm:grid-cols-2 sm:gap-3">
                  <Input
                    label=" "
                    value={formData.landlord_name || ""}
                    onChange={(e) =>
                      handleInputChange("landlord_name", e.target.value)
                    }
                    placeholder=""
                    className="text-sm sm:text-base h-11 sm:h-12"
                  />

                  <Input
                    label=" "
                    value={formData.landlord_phone || ""}
                    onChange={(e) =>
                      handleInputChange("landlord_phone", e.target.value)
                    }
                    placeholder="010-1234-5678"
                    className="text-sm sm:text-base h-11 sm:h-12"
                  />
                </div>
              </div>

              {/*    */}
              <div className="bg-gradient-to-r from-gray-50 to-slate-50 rounded-lg p-3 sm:p-4 border border-gray-100">
                <div className="flex items-center space-x-2 mb-3 sm:mb-4">
                  <div className="p-1.5 bg-gray-600 rounded-md">
                    <FileText className="w-4 h-4 text-white" />
                  </div>
                  <div className="min-w-0 flex-1">
                    <h3 className="text-sm sm:text-base font-semibold text-gray-900">
                       
                    </h3>
                    <p className="text-xs text-gray-600 hidden sm:block">
                         
                    </p>
                  </div>
                </div>

                <div>
                  <label className="block text-xs sm:text-sm font-medium text-gray-700 mb-2">
                     
                  </label>
                  <textarea
                    value={formData.description || ""}
                    onChange={(e) =>
                      handleInputChange("description", e.target.value)
                    }
                    placeholder="    &#10;: , ,  ,   "
                    rows={3}
                    className="w-full px-3 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-gray-500 focus:border-gray-500 text-xs sm:text-sm resize-none transition-colors"
                  />
                </div>
              </div>

              {/*      */}
              <div className="flex flex-col-reverse sm:flex-row sm:justify-end space-y-reverse space-y-3 sm:space-y-0 sm:space-x-3 pt-4 border-t border-gray-200 mt-4">
                <Button
                  type="button"
                  variant="outline"
                  onClick={onClose}
                  disabled={loading}
                  className="w-full sm:w-auto px-4 sm:px-6 py-3 sm:py-2 text-sm sm:text-base active:scale-95"
                >
                  
                </Button>
                <Button
                  type="submit"
                  variant="primary"
                  loading={loading}
                  leftIcon={<Save size={16} />}
                  className="w-full sm:w-auto px-4 sm:px-6 py-3 sm:py-2 text-sm sm:text-base bg-blue-600 hover:bg-blue-700 focus:ring-blue-500 active:scale-95"
                >
                   
                </Button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </Modal>
  );
};
