import { supabase } from "./supabase";
import { logger } from "@/utils/logger";

//   
interface EmailInvitationData {
  invitationToken: string;
  recipientEmail: string;
  inviterName: string;
  teamName: string;
  role: string;
  message?: string;
}

//    
const sendInvitationEmail = async (data: EmailInvitationData) => {
  const { data: result, error } = await supabase.functions.invoke(
    "send-team-invitation",
    {
      body: data,
    },
  );

  if (error) {
    logger.error("Email sending failed", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "sendInvitationEmail",
    });
    throw error;
  }

  return result;
};

export interface TeamMember {
  id: string;
  tenant_id: string;
  name: string;
  email?: string;
  phone?: string;
  role: "owner" | "admin" | "member" | "viewer";
  status: "active" | "inactive" | "suspended";
  joined_at: string;
  invited_by?: string;
  company?: string;
}

export interface TeamInvitation {
  id: string;
  tenant_id: string;
  inviter_id: string;
  email: string;
  role: "owner" | "admin" | "member" | "viewer";
  status: "pending" | "accepted" | "declined" | "expired";
  invitation_token: string;
  expires_at: string;
  created_at: string;
  inviter_name?: string;
}

export interface CreateInvitationData {
  email: string;
  role: "admin" | "member" | "viewer";
  message?: string;
}

export interface AddExistingMemberData {
  user_id: string;
  role: "admin" | "member" | "viewer";
}

export interface UpdateMemberData {
  name?: string;
  email?: string;
  phone?: string;
  company?: string;
}

//    
export const getTeamMembers = async (userId: string) => {
  try {
    //  tenant_id 
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id")
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //    ( )
    const { data: members, error } = await supabase
      .from("user_profiles")
      .select(
        `
        id,
        tenant_id,
        name,
        email,
        phone,
        role,
        status,
        joined_at,
        invited_by,
        company
      `,
      )
      .eq("tenant_id", userProfile.tenant_id)
      .order("joined_at", { ascending: false });

    if (error) throw error;

    return members || [];
  } catch (error) {
    logger.error("Failed to fetch team members", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "getTeamMembers",
    });
    throw error;
  }
};

//    
export const getTeamInvitations = async (userId: string) => {
  try {
    //  tenant_id 
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id")
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //   
    const { data: invitations, error } = await supabase
      .from("team_invitations")
      .select(
        `
        id,
        tenant_id,
        inviter_id,
        email,
        role,
        status,
        invitation_token,
        expires_at,
        created_at
      `,
      )
      .eq("tenant_id", userProfile.tenant_id)
      .order("created_at", { ascending: false });

    if (error) throw error;

    //    
    if (invitations && invitations.length > 0) {
      const inviterIds = [...new Set(invitations.map((inv) => inv.inviter_id))];
      const { data: inviters } = await supabase
        .from("user_profiles")
        .select("id, name")
        .in("id", inviterIds);

      const inviterMap =
        inviters?.reduce(
          (acc, inviter) => {
            acc[inviter.id] = inviter.name;
            return acc;
          },
          {} as Record<string, string>,
        ) || {};

      return invitations.map((inv) => ({
        ...inv,
        inviter_name: inviterMap[inv.inviter_id] || "   ",
      }));
    }

    return [];
  } catch (error) {
    logger.error("Failed to fetch team invitations", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "getTeamInvitations",
    });
    throw error;
  }
};

//   
export const inviteTeamMember = async (
  userId: string,
  invitationData: CreateInvitationData,
) => {
  try {
    //  tenant_id   (  )
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select(
        `
        tenant_id, 
        role, 
        name,
        tenant:tenant_id(name)
      `,
      )
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //   (owner  admin  )
    if (userProfile.role !== "owner" && userProfile.role !== "admin") {
      throw new Error("   .");
    }

    //    
    const { data: existingMember } = await supabase
      .from("user_profiles")
      .select("id")
      .eq("tenant_id", userProfile.tenant_id)
      .eq("email", invitationData.email)
      .single();

    if (existingMember) {
      throw new Error("   .");
    }

    //    
    const { data: existingInvitation } = await supabase
      .from("team_invitations")
      .select("id, status")
      .eq("tenant_id", userProfile.tenant_id)
      .eq("email", invitationData.email)
      .eq("status", "pending")
      .single();

    if (existingInvitation) {
      throw new Error("  .");
    }

    //   
    const { data: invitation, error: inviteError } = await supabase
      .from("team_invitations")
      .insert({
        tenant_id: userProfile.tenant_id,
        inviter_id: userId,
        email: invitationData.email,
        role: invitationData.role,
      })
      .select()
      .single();

    if (inviteError) throw inviteError;

    //  
    try {
      await sendInvitationEmail({
        invitationToken: invitation.invitation_token,
        recipientEmail: invitationData.email,
        inviterName: userProfile.name,
        teamName:
          (userProfile && "tenant" in userProfile && userProfile.tenant
            ? (userProfile.tenant as any)?.name
            : null) || "",
        role: invitationData.role,
        message: invitationData.message,
      });
    } catch (emailError) {
      logger.warn("Email sending failed but invitation created", {
        error:
          emailError instanceof Error ? emailError.message : "Unknown error",
        component: "teamService",
        action: "inviteTeamMember",
      });
      //      
    }

    //   
    await supabase.from("team_activity_logs").insert({
      tenant_id: userProfile.tenant_id,
      user_id: userId,
      action: "invitation_sent",
      details: {
        invited_email: invitationData.email,
        role: invitationData.role,
        message: invitationData.message,
      },
    });

    return {
      ...invitation,
      inviteUrl: `${typeof window !== "undefined" ? window.location.origin : "https://summi3-2mk8sy7pu-daesung75-6440s-projects.vercel.app"}/team/invite?token=${invitation.invitation_token}`,
    };
  } catch (error) {
    logger.error("Failed to invite team member", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "inviteTeamMember",
    });
    throw error;
  }
};

//    
export const addExistingMember = async (
  userId: string,
  memberData: AddExistingMemberData,
) => {
  try {
    //  tenant_id  
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id, role")
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //  
    if (userProfile.role !== "owner" && userProfile.role !== "admin") {
      throw new Error("   .");
    }

    //    
    const { data: targetUser, error: targetError } = await supabase
      .from("user_profiles")
      .select("id, name, email, tenant_id")
      .eq("id", memberData.user_id)
      .single();

    if (targetError) {
      throw new Error("  .");
    }

    //     
    if (targetUser.tenant_id === userProfile.tenant_id) {
      throw new Error("   .");
    }

    //      
    //      

    //  
    const { data: updatedMember, error: updateError } = await supabase
      .from("user_profiles")
      .update({
        tenant_id: userProfile.tenant_id,
        role: memberData.role,
        invited_by: userId,
        joined_at: new Date().toISOString(),
      })
      .eq("id", memberData.user_id)
      .select()
      .single();

    if (updateError) throw updateError;

    //   
    await supabase.from("team_activity_logs").insert({
      tenant_id: userProfile.tenant_id,
      user_id: userId,
      action: "member_added",
      details: {
        added_user_id: memberData.user_id,
        added_user_name: targetUser.name,
        role: memberData.role,
        previous_tenant_id: targetUser.tenant_id, //    
        transferred_from_team: targetUser.tenant_id ? true : false,
      },
    });

    return updatedMember;
  } catch (error) {
    logger.error("Failed to add team member", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "addTeamMember",
    });
    throw error;
  }
};

//   
export const updateMemberRole = async (
  userId: string,
  memberId: string,
  newRole: TeamMember["role"],
) => {
  try {
    //  tenant_id  
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id, role")
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //   (owner   ,  admin member/viewer  )
    if (
      userProfile.role !== "owner" &&
      !(userProfile.role === "admin" && ["member", "viewer"].includes(newRole))
    ) {
      throw new Error("   .");
    }

    //      
    if (userId === memberId) {
      throw new Error("    .");
    }

    //   
    const { data: updatedMember, error: updateError } = await supabase
      .from("user_profiles")
      .update({ role: newRole })
      .eq("id", memberId)
      .eq("tenant_id", userProfile.tenant_id)
      .select()
      .single();

    if (updateError) throw updateError;

    //   
    await supabase.from("team_activity_logs").insert({
      tenant_id: userProfile.tenant_id,
      user_id: userId,
      action: "role_changed",
      details: {
        target_user_id: memberId,
        new_role: newRole,
      },
    });

    return updatedMember;
  } catch (error) {
    logger.error("Failed to change member role", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "changeMemberRole",
    });
    throw error;
  }
};

//   
export const removeMember = async (userId: string, memberId: string) => {
  try {
    //  tenant_id  
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id, role")
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //   (owner  admin  )
    if (userProfile.role !== "owner" && userProfile.role !== "admin") {
      throw new Error("   .");
    }

    //     
    if (userId === memberId) {
      throw new Error("    .");
    }

    //    
    const { data: memberToRemove, error: memberError } = await supabase
      .from("user_profiles")
      .select("name, role")
      .eq("id", memberId)
      .eq("tenant_id", userProfile.tenant_id)
      .single();

    if (memberError) {
      throw new Error("     .");
    }

    // owner   
    if (memberToRemove.role === "owner") {
      throw new Error("    .");
    }

    //    
    const { data: newTenant, error: tenantError } = await supabase
      .from("tenants")
      .insert({
        name: `${memberToRemove.name} `,
      })
      .select()
      .single();

    if (tenantError) throw tenantError;

    //    
    const { error: updateError } = await supabase
      .from("user_profiles")
      .update({
        tenant_id: newTenant.id,
        role: "owner",
        invited_by: null,
      })
      .eq("id", memberId);

    if (updateError) throw updateError;

    //   
    await supabase.from("team_activity_logs").insert({
      tenant_id: userProfile.tenant_id,
      user_id: userId,
      action: "member_removed",
      details: {
        removed_user_id: memberId,
        removed_user_name: memberToRemove.name,
      },
    });

    return true;
  } catch (error) {
    logger.error("Failed to remove team member", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "removeTeamMember",
    });
    throw error;
  }
};

//  
export const cancelInvitation = async (
  userId: string,
  invitationId: string,
) => {
  try {
    //  tenant_id  
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id, role")
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //  
    if (userProfile.role !== "owner" && userProfile.role !== "admin") {
      throw new Error("   .");
    }

    //  
    const { error } = await supabase
      .from("team_invitations")
      .delete()
      .eq("id", invitationId)
      .eq("tenant_id", userProfile.tenant_id);

    if (error) throw error;

    return true;
  } catch (error) {
    logger.error("Failed to cancel invitation", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "cancelInvitation",
    });
    throw error;
  }
};

//   (  )
export const searchUsers = async (query: string, currentUserId: string) => {
  try {
    //   tenant_id 
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id")
      .eq("id", currentUserId)
      .single();

    if (userError) throw userError;

    //     (    )
    const { data: users, error } = await supabase
      .from("user_profiles")
      .select("id, name, email, company")
      .or(`name.ilike.%${query}%,email.ilike.%${query}%`)
      .or(`tenant_id.is.null,tenant_id.neq.${userProfile.tenant_id}`)
      .neq("id", currentUserId)
      .limit(10);

    if (error) throw error;

    return users || [];
  } catch (error) {
    logger.error("Failed to search users", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "searchUsers",
    });
    throw error;
  }
};

//   
export const updateMemberInfo = async (
  userId: string,
  memberId: string,
  updateData: UpdateMemberData,
) => {
  try {
    //  tenant_id  
    const { data: userProfile, error: userError } = await supabase
      .from("user_profiles")
      .select("tenant_id, role")
      .eq("id", userId)
      .single();

    if (userError) throw userError;

    //   (owner  admin     ,   )
    if (
      userId !== memberId &&
      userProfile.role !== "owner" &&
      userProfile.role !== "admin"
    ) {
      throw new Error("    .");
    }

    //   
    const { data: updatedMember, error: updateError } = await supabase
      .from("user_profiles")
      .update(updateData)
      .eq("id", memberId)
      .eq("tenant_id", userProfile.tenant_id)
      .select()
      .single();

    if (updateError) throw updateError;

    //    (  )
    if (userId !== memberId) {
      await supabase.from("team_activity_logs").insert({
        tenant_id: userProfile.tenant_id,
        user_id: userId,
        action: "member_info_updated",
        details: {
          target_user_id: memberId,
          updated_fields: Object.keys(updateData),
        },
      });
    }

    return updatedMember;
  } catch (error) {
    logger.error("Failed to update member info", {
      error: error instanceof Error ? error.message : "Unknown error",
      component: "teamService",
      action: "updateMemberInfo",
    });
    throw error;
  }
};

