// ============================================================================

import React, { useState, useEffect } from "react";
import { Calendar, MapPin, Users, Tag, AlertCircle } from "lucide-react";
import { Button, Input, Modal } from "@/components/ui";
import { Schedule, ScheduleFormData, ScheduleCategory, SchedulePriority } from "@/types/schedule";
import { useAuth } from "@/contexts/AuthContext";
// import { useTenant } from "@/contexts/TenantContext"; //   
import { scheduleService } from "@/services/scheduleService";
import { useProperties } from "@/hooks/useProperties";

interface ScheduleFormProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (schedule: Schedule) => void;
  initialData?: Schedule | null;
  selectedDate?: Date;
}

const ScheduleForm: React.FC<ScheduleFormProps> = ({
  isOpen,
  onClose,
  onSubmit,
  initialData,
  selectedDate,
}) => {
  const { user, loading: authLoading, session, getCurrentUser } = useAuth();
  // const { tenant } = useTenant(); //   
  const { properties } = useProperties();
  
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  
  const [formData, setFormData] = useState<ScheduleFormData>({
    title: "",
    description: "",
    start_date: "",
    end_date: "",
    all_day: false,
    category: "other",
    priority: "medium",
    property_id: "",
    attendees: [],
    location: "",
  });

  //   
  useEffect(() => {
    if (initialData) {
      setFormData({
        title: initialData.title,
        description: initialData.description || "",
        start_date: initialData.start_date,
        end_date: initialData.end_date,
        all_day: initialData.all_day,
        category: initialData.category,
        priority: initialData.priority,
        property_id: initialData.property_id || "",
        attendees: initialData.attendees || [],
        location: initialData.location || "",
      });
    } else {
      //      
      const now = new Date();
      
      //     
      let startTime: Date;
      let endTime: Date;
      
      if (selectedDate) {
        //    :     
        startTime = new Date(selectedDate);
        //        
        const currentHour = now.getHours();
        const currentMinute = now.getMinutes();
        
        if (selectedDate.toDateString() === now.toDateString()) {
          //       
          startTime.setHours(currentHour + (currentMinute > 0 ? 1 : 0), 0, 0, 0);
        } else {
          //    9 
          startTime.setHours(9, 0, 0, 0);
        }
      } else {
        //      :     
        startTime = new Date(now);
        startTime.setHours(now.getHours() + 1, 0, 0, 0);
      }
      
      //     1 
      endTime = new Date(startTime);
      endTime.setHours(startTime.getHours() + 1);
      
      const startDateTime = startTime.toISOString().slice(0, 16); // YYYY-MM-DDTHH:MM 
      const endDateTime = endTime.toISOString().slice(0, 16);
      
      console.log("   :", {
        selectedDate: selectedDate?.toISOString(),
        clickedDate: selectedDate?.toDateString(),
        currentDate: now.toDateString(),
        isToday: selectedDate?.toDateString() === now.toDateString(),
        startDateTime,
        endDateTime
      });
      
      setFormData(prev => ({
        ...prev,
        start_date: startDateTime,
        end_date: endDateTime,
      }));
    }
  }, [initialData, selectedDate]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    console.log(" handleSubmit  !");
    console.log("  formData:", formData);
    console.log("   :", { 
      authLoading, 
      hasUser: !!user, 
      userId: user?.id,
      hasSession: !!session 
    });
    
    //    
    if (authLoading) {
      setError("   .    .");
      return;
    }
    
    //   
    if (!formData.title.trim()) {
      setError("  .");
      return;
    }
    
    if (!formData.start_date) {
      setError("  .");
      return;
    }
    
    if (!formData.end_date) {
      setError("  .");
      return;
    }
    
    if (new Date(formData.start_date) >= new Date(formData.end_date)) {
      setError("     .");
      return;
    }
    
    console.log("    ");
    
    setIsSubmitting(true);
    setError(null);

    try {
      // 1.     (  )
      console.log("    ...");
      
      let currentUser = null;
      
      //  getCurrentUser  
      const authUser = await getCurrentUser();
      
      if (!authUser) {
        console.error("   ");
        setError("  .    .");
        return;
      }
      
      currentUser = { id: authUser.id, email: authUser.email };

      console.log("  :", {
        id: currentUser.id,
        email: currentUser.email
      });

      // 2.  tenant_id  ( ID tenant_id )
      const tenantId = currentUser.id; //     
      console.log("  ID:", tenantId);

      // 3.    (UUID  )
      const cleanedFormData = { ...formData };
      
      // property_id     (DB NULL )
      if (!cleanedFormData.property_id || cleanedFormData.property_id.trim() === '') {
        delete cleanedFormData.property_id;
      }
      
      // attendees     (DB NULL )
      if (Array.isArray(cleanedFormData.attendees) && cleanedFormData.attendees.length === 0) {
        delete cleanedFormData.attendees;
      }
      
      console.log("   :", cleanedFormData);

      // 4.  /
      let schedule: Schedule;
      
      if (initialData) {
        console.log("   ...", {
          scheduleId: initialData.id,
          tenantId,
          currentUserId: currentUser.id,
          originalTenantId: initialData.tenant_id,
          originalCreatedBy: initialData.created_by
        });
        
        //      (  )
        if (initialData.tenant_id !== tenantId || initialData.created_by !== currentUser.id) {
          console.error("    ");
          setError("    .");
          return;
        }
        
        schedule = await scheduleService.updateSchedule(initialData.id, cleanedFormData, currentUser.id);
      } else {
        console.log("   ...");
        schedule = await scheduleService.createSchedule(
          tenantId, //  ID  ID 
          currentUser.id,
          cleanedFormData
        );
      }
      
      console.log("   :", schedule);
      
      // 4. UI    
      onSubmit(schedule);
      onClose();
      
    } catch (err: any) {
      console.error("   :", err);
      console.error("  :", {
        message: err.message,
        stack: err.stack,
        name: err.name,
        code: err.code
      });
      
      //    
      if (err.message?.includes("JWT") || err.message?.includes(" ")) {
        setError(" .    .");
      } else if (err.message?.includes("permission") || err.message?.includes(" ")) {
        setError("    .     .");
      } else if (err.message?.includes("  ")) {
        setError("   .      .");
      } else {
        setError(err.message || "    .");
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleChange = (field: keyof ScheduleFormData, value: any) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    
    //       
    if (field === "all_day" && value) {
      const startDate = formData.start_date.split('T')[0];
      const endDate = formData.end_date.split('T')[0];
      setFormData(prev => ({
        ...prev,
        start_date: `${startDate}T00:00`,
        end_date: `${endDate}T23:59`,
      }));
    }
  };

  const categoryOptions: { value: ScheduleCategory; label: string; icon: React.ReactNode }[] = [
    { value: "property_viewing", label: " ", icon: <MapPin className="w-4 h-4" /> },
    { value: "contract_signing", label: " ", icon: <Tag className="w-4 h-4" /> },
    { value: "maintenance", label: "", icon: <AlertCircle className="w-4 h-4" /> },
    { value: "client_meeting", label: " ", icon: <Users className="w-4 h-4" /> },
    { value: "team_meeting", label: " ", icon: <Users className="w-4 h-4" /> },
    { value: "personal", label: " ", icon: <Calendar className="w-4 h-4" /> },
    { value: "other", label: "", icon: <Tag className="w-4 h-4" /> },
  ];

  const priorityOptions: { value: SchedulePriority; label: string; color: string }[] = [
    { value: "low", label: "", color: "text-gray-600" },
    { value: "medium", label: "", color: "text-blue-600" },
    { value: "high", label: "", color: "text-orange-600" },
    { value: "urgent", label: "", color: "text-red-600" },
  ];

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      title={initialData ? " " : "  "}
      size="lg"
    >
      <form onSubmit={handleSubmit} className="p-6 space-y-6">
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-3">
            <p className="text-sm text-red-600">{error}</p>
          </div>
        )}

        {/*  */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
              *
          </label>
          <Input
            value={formData.title}
            onChange={(e) => handleChange("title", e.target.value)}
            placeholder="  "
            required
          />
        </div>

        {/*  */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <textarea
            value={formData.description}
            onChange={(e) => handleChange("description", e.target.value)}
            placeholder="    "
            className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm resize-none h-20"
          />
        </div>

        {/*  */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
             *
          </label>
          <select
            value={formData.category}
            onChange={(e) => handleChange("category", e.target.value as ScheduleCategory)}
            className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm"
            required
          >
            {categoryOptions.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        </div>

        {/*  */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <select
            value={formData.priority}
            onChange={(e) => handleChange("priority", e.target.value as SchedulePriority)}
            className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm"
          >
            {priorityOptions.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        </div>

        {/*   */}
        {(formData.category === "property_viewing" || formData.category === "contract_signing") && (
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
               
            </label>
            <select
              value={formData.property_id}
              onChange={(e) => handleChange("property_id", e.target.value)}
              className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm"
            >
              <option value="">  ()</option>
              {properties.map((property) => (
                <option key={property.id} value={property.id}>
                  {property.title} - {property.address}
                </option>
              ))}
            </select>
          </div>
        )}

        {/*    */}
        <div className="flex items-center space-x-2">
          <input
            type="checkbox"
            id="all_day"
            checked={formData.all_day}
            onChange={(e) => handleChange("all_day", e.target.checked)}
            className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
          />
          <label htmlFor="all_day" className="text-sm text-gray-700">
             
          </label>
        </div>

        {/*   */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
                *
            </label>
            <Input
              type={formData.all_day ? "date" : "datetime-local"}
              value={formData.all_day ? formData.start_date.split('T')[0] : formData.start_date}
              onChange={(e) => {
                const value = formData.all_day ? `${e.target.value}T00:00` : e.target.value;
                handleChange("start_date", value);
              }}
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
                *
            </label>
            <Input
              type={formData.all_day ? "date" : "datetime-local"}
              value={formData.all_day ? formData.end_date.split('T')[0] : formData.end_date}
              onChange={(e) => {
                const value = formData.all_day ? `${e.target.value}T23:59` : e.target.value;
                handleChange("end_date", value);
              }}
              required
            />
          </div>
        </div>

        {/*  */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <Input
            value={formData.location}
            onChange={(e) => handleChange("location", e.target.value)}
            placeholder="  "
            leftIcon={<MapPin className="w-4 h-4" />}
          />
        </div>

        {/*   */}
        <div className="flex justify-end space-x-2 pt-4 border-t border-gray-200">
          <Button
            type="button"
            onClick={onClose}
            variant="outline"
            disabled={isSubmitting || authLoading}
          >
            
          </Button>
          <Button
            type="submit"
            variant="tenant"
            disabled={isSubmitting || authLoading}
            onClick={() => console.log("   !")}
          >
            {authLoading ? "  ..." : isSubmitting ? " ..." : initialData ? "" : ""}
          </Button>
        </div>
      </form>
    </Modal>
  );
};

export { ScheduleForm };