import React, { useState } from "react";
import { X, Copy, Mail, Check } from "lucide-react";

interface InviteLinkModalProps {
  isOpen: boolean;
  onClose: () => void;
  inviteData: {
    email: string;
    inviteUrl: string;
    role: string;
    teamName?: string;
  };
}

export const InviteLinkModal: React.FC<InviteLinkModalProps> = ({
  isOpen,
  onClose,
  inviteData,
}) => {
  const [copied, setCopied] = useState(false);

  if (!isOpen) return null;

  const handleCopyLink = async () => {
    try {
      await navigator.clipboard.writeText(inviteData.inviteUrl);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (error) {
      console.error("  :", error);
    }
  };

  const handleEmailShare = () => {
    const subject = `[${inviteData.teamName || ""}]  - ${getRoleLabel(inviteData.role)} `;
    const body = `!

${inviteData.teamName || ""} ${getRoleLabel(inviteData.role)}  .

    :
${inviteData.inviteUrl}

.`;

    const mailtoUrl = `mailto:${inviteData.email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
    window.open(mailtoUrl);
  };

  const getRoleLabel = (role: string) => {
    const labels = {
      admin: "",
      member: "",
      viewer: "",
    };
    return labels[role as keyof typeof labels] || role;
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg w-full max-w-md mx-4">
        {/*  */}
        <div className="border-b border-gray-200 px-6 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-lg font-semibold">   </h2>
              <p className="text-sm text-gray-600">
                  
              </p>
            </div>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/*  */}
        <div className="p-6">
          <div className="space-y-4">
            {/*   */}
            <div className="bg-blue-50 rounded-lg p-4">
              <div className="flex items-center space-x-2 mb-2">
                <Mail className="w-4 h-4 text-blue-600" />
                <span className="font-medium text-blue-900"> </span>
              </div>
              <div className="text-sm text-blue-700">
                <p>
                  <strong>:</strong> {inviteData.email}
                </p>
                <p>
                  <strong>:</strong> {getRoleLabel(inviteData.role)}
                </p>
                {inviteData.teamName && (
                  <p>
                    <strong>:</strong> {inviteData.teamName}
                  </p>
                )}
              </div>
            </div>

            {/*   */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 
              </label>
              <div className="flex">
                <input
                  type="text"
                  value={inviteData.inviteUrl}
                  readOnly
                  className="flex-1 p-3 border border-gray-300 rounded-l-lg bg-gray-50 text-sm"
                />
                <button
                  onClick={handleCopyLink}
                  className={`px-4 py-3 border border-l-0 border-gray-300 rounded-r-lg transition-colors ${
                    copied
                      ? "bg-green-100 text-green-700 border-green-300"
                      : "bg-gray-50 text-gray-700 hover:bg-gray-100"
                  }`}
                  title=" "
                >
                  {copied ? (
                    <Check className="w-4 h-4" />
                  ) : (
                    <Copy className="w-4 h-4" />
                  )}
                </button>
              </div>
              {copied && (
                <p className="text-sm text-green-600 mt-1">
                    !
                </p>
              )}
            </div>

            {/*   */}
            <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
              <div className="flex items-start space-x-2">
                <div className="text-yellow-600"></div>
                <div className="text-sm text-yellow-800">
                  <p className="font-medium mb-1">  </p>
                  <ul className="space-y-1 text-xs">
                    <li>   7  </li>
                    <li>     </li>
                    <li>   </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="border-t border-gray-200 px-6 py-4 flex space-x-3">
          <button
            onClick={handleEmailShare}
            className="flex-1 bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors flex items-center justify-center space-x-2"
          >
            <Mail className="w-4 h-4" />
            <span> </span>
          </button>
          <button
            onClick={onClose}
            className="px-6 py-2 text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};
