import { createClient } from "@supabase/supabase-js";
import type { Database } from "@/types/database";
import { logger } from "@/utils/logger";

const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;

if (!supabaseUrl || !supabaseAnonKey) {
  throw new Error(
    "Supabase URL Anon Key    .\n" +
      "VITE_SUPABASE_URL VITE_SUPABASE_ANON_KEY .env  .",
  );
}

// Supabase  
export const supabase = createClient<Database>(supabaseUrl, supabaseAnonKey, {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true,
    storageKey: "propertydesk-auth",
  },
  global: {
    headers: {
      "X-Client": "PropertyDesk-SaaS",
    },
  },
});

//    RLS  
export const setTenantContext = async (tenantId: string) => {
  const { error } = await supabase.rpc("set_current_tenant_id", {
    tenant_id: tenantId,
  });

  if (error) {
    console.error("   :", error);
    throw error;
  }
};

//    
export const clearTenantContext = async () => {
  const { error } = await supabase.rpc("clear_current_tenant_id");

  if (error) {
    console.error("   :", error);
    throw error;
  }
};

//    
export const createTenantChannel = (tenantId: string, table: string) => {
  return supabase.channel(`tenant-${tenantId}-${table}`).on(
    "postgres_changes",
    {
      event: "*",
      schema: "public",
      table,
      filter: `tenant_id=eq.${tenantId}`,
    },
    (payload) => {
      logger.info("  :", payload);
    },
  );
};

//    (  )
export const uploadFile = async (
  file: File,
  bucket: string,
  path: string,
  tenantId: string,
) => {
  const fileExt = file.name.split(".").pop();
  const fileName = `${Date.now()}-${Math.random().toString(36).substring(2)}.${fileExt}`;
  const filePath = `${tenantId}/${path}/${fileName}`;

  const { data, error } = await supabase.storage
    .from(bucket)
    .upload(filePath, file, {
      cacheControl: "3600",
      upsert: false,
    });

  if (error) {
    throw error;
  }

  //  URL 
  const {
    data: { publicUrl },
  } = supabase.storage.from(bucket).getPublicUrl(filePath);

  return {
    path: data.path,
    url: publicUrl,
    fileName,
  };
};

//   
export const handleSupabaseError = (error: any) => {
  console.error("Supabase :", error);

  //    
  const errorMessages: Record<string, string> = {
    "Invalid login credentials": "    .",
    "Email not confirmed": "  .",
    "User already registered": "   .",
    "Password should be at least 6 characters":
      "  6  .",
    "Unable to validate email address": "   .",
    "duplicate key value": " .",
    "foreign key constraint": "   .",
  };

  const message =
    errorMessages[error.message] ||
    error.message ||
    "    .";

  return {
    code: error.code || "UNKNOWN_ERROR",
    message,
    details: error.details || error.hint,
  };
};

//  
export const checkSupabaseHealth = async () => {
  try {
    const { error } = await supabase
      .from("tenants")
      .select("id")
      .limit(1)
      .single();

    return {
      status: "healthy" as const,
      database: !error,
      timestamp: new Date().toISOString(),
    };
  } catch (error) {
    return {
      status: "unhealthy" as const,
      database: false,
      error: handleSupabaseError(error),
      timestamp: new Date().toISOString(),
    };
  }
};
