// ============================================================================

import React, { useState, useEffect } from "react";
import { Home, Users, TrendingUp, Calendar } from "lucide-react";
import { useNavigate } from "react-router-dom";
import { Card, CardContent, Badge } from "@/components/ui";
import { useTenant } from "@/contexts/TenantContext";
import { useAuth } from "@/contexts/AuthContext";
import { usePropertyStats, useProperties } from "@/hooks/useProperties";
// import { PropertyTrendChart, PropertyTypeChart } from "@/components/charts"; //  
import { StatCard, ScheduleSummary } from "@/components/dashboard";
import { PropertyCard, PropertyDetailModal } from "@/components/property";
import { PropertyEditForm } from "@/components/forms/PropertyEditForm";
import { Property, UpdatePropertyData } from "@/types/property";
import { Schedule } from "@/types/schedule";
import * as teamService from "@/services/teamService";
import { scheduleService } from "@/services/scheduleService";

const DashboardPage: React.FC = () => {
  const navigate = useNavigate();
  const { tenant } = useTenant();
  const { user } = useAuth();
  const {
    stats,
    loading: statsLoading,
    error: statsError,
  } = usePropertyStats();
  const { properties, loading, updateProperty, deleteProperty, refreshProperties } =
    useProperties();

  //    
  const [teamMemberCount, setTeamMemberCount] = useState<number>(1);
  const [teamLoading, setTeamLoading] = useState(true);

  //   
  const [todaySchedules, setTodaySchedules] = useState<Schedule[]>([]);
  const [upcomingSchedules, setUpcomingSchedules] = useState<Schedule[]>([]);
  const [schedulesLoading, setSchedulesLoading] = useState(true);

  //    
  const [selectedProperty, setSelectedProperty] = useState<Property | null>(
    null,
  );
  const [isModalOpen, setIsModalOpen] = useState(false);

  //    
  const [editFormOpen, setEditFormOpen] = useState(false);
  const [editLoading, setEditLoading] = useState(false);
  const [editingProperty, setEditingProperty] = useState<Property | null>(null);

  //   
  useEffect(() => {
    const fetchTeamMembers = async () => {
      if (!user?.id) return;

      try {
        setTeamLoading(true);
        const members = await teamService.getTeamMembers(user.id);
        setTeamMemberCount(members.length);
      } catch (error) {
        console.error("   :", error);
        setTeamMemberCount(1); // 
      } finally {
        setTeamLoading(false);
      }
    };

    fetchTeamMembers();
  }, [user?.id]);

  //   
  useEffect(() => {
    const fetchSchedules = async () => {
      if (!user?.id) return;

      try {
        setSchedulesLoading(true);
        
        //  ID  ID 
        const tenantId = user.id;
        
        const [todayData, upcomingData] = await Promise.all([
          scheduleService.getTodaySchedules(tenantId, 3),
          scheduleService.getUpcomingSchedules(tenantId, 3)
        ]);
        
        setTodaySchedules(todayData);
        setUpcomingSchedules(upcomingData);
      } catch (error) {
        console.error("  :", error);
        setTodaySchedules([]);
        setUpcomingSchedules([]);
      } finally {
        setSchedulesLoading(false);
      }
    };

    fetchSchedules();
  }, [user?.id]);

  //    
  const handleViewAllSchedules = () => {
    navigate('/schedule');
  };

  //   
  const handlePropertyClick = (property: Property) => {
    setSelectedProperty(property);
    setIsModalOpen(true);
  };

  //   
  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedProperty(null);
  };

  //   
  const handleEditProperty = async (data: UpdatePropertyData) => {
    if (!editingProperty) return;

    try {
      setEditLoading(true);
      await updateProperty(editingProperty.id, data);
      setEditFormOpen(false);
      setEditingProperty(null);
      //  
      await refreshProperties();
      alert("   !");
    } catch (error) {
      console.error("  :", error);
      const errorMessage =
        error instanceof Error
          ? error.message
          : "    .";
      alert(`   : ${errorMessage}`);
      throw error;
    } finally {
      setEditLoading(false);
    }
  };

  //    
  const handleOpenEditForm = (property: Property) => {
    console.log("    :", property.title);
    setEditingProperty(property);
    setEditFormOpen(true);
    setIsModalOpen(false); //   
  };

  //   
  const handleConfirmDelete = async (property: Property) => {
    console.log("   :", property.title);

    const confirmDelete = window.confirm(
      `'${property.title}'   ?\n\n    .`,
    );

    if (confirmDelete) {
      try {
        await deleteProperty(property.id);
        setIsModalOpen(false);
        setSelectedProperty(null);
        //  
        await refreshProperties();
        alert("   !");
      } catch (error) {
        console.error("  :", error);
        const errorMessage =
          error instanceof Error
            ? error.message
            : "    .";
        alert(`   : ${errorMessage}`);
      }
    }
  };

  //     
  const dashboardStats = [
    {
      title: " ",
      value: (stats?.total ?? 0).toString(),
      change: "+12%",
      icon: Home,
      color: "text-blue-600",
    },
    {
      title: " ",
      value: (stats?.active ?? 0).toString(),
      change: "+5%",
      icon: TrendingUp,
      color: "text-green-600",
    },
    {
      title: "",
      value: teamMemberCount.toString(),
      change: teamMemberCount > 1 ? `+${teamMemberCount - 1}` : "0",
      icon: Users,
      color: "text-purple-600",
    },
    {
      title: "  ",
      value: (stats?.this_month ?? 0).toString(),
      change: "+18%",
      icon: Calendar,
      color: "text-orange-600",
    },
  ];

  const recentProperties = [
    {
      id: 1,
      title: "   ",
      type: "",
      transactionType: "",
      price: "35",
      //    
      createdAt: "2 ",
    },
    {
      id: 2,
      title: "  ",
      type: "",
      transactionType: "",
      price: "2 1",
      //    
      createdAt: "5 ",
    },
    {
      id: 3,
      title: "  ",
      type: "",
      transactionType: "",
      price: "1000/65",
      //    
      createdAt: "1 ",
    },
  ];

  //   
  if (loading || statsLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-4 sm:space-y-6 pb-20 sm:pb-6">
      {/*   -   */}
      <div className="px-1 sm:px-0">
        <h1 className="text-lg sm:text-2xl font-bold leading-tight">
          , {user?.name || ""}! 
        </h1>
        <h2 className="text-sm sm:text-base text-gray-600 mt-1 lg:hidden">
          {tenant?.name || " "}
        </h2>
        <h2 className="hidden lg:block text-2xl font-bold mt-1">
          {tenant?.name || " "}    
        </h2>
        <p className="text-sm sm:text-base text-gray-600 mt-1">   </p>
      </div>

      {/*   -   */}
      {statsLoading ? (
        <div className="grid grid-cols-2 sm:grid-cols-4 gap-3 sm:gap-4 lg:gap-6">
          {[1, 2, 3, 4].map((i) => (
            <Card key={i} className="animate-pulse">
              <CardContent className="p-3 sm:p-4 lg:p-6">
                <div className="flex flex-col space-y-2">
                  <div className="h-3 sm:h-4 bg-gray-200 rounded w-3/4"></div>
                  <div className="h-6 sm:h-8 bg-gray-200 rounded w-1/2"></div>
                  <div className="h-3 bg-gray-200 rounded w-1/3 hidden sm:block"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : statsError ? (
        <Card className="p-4 sm:p-6">
          <p className="text-red-600 text-center text-sm sm:text-base">{statsError}</p>
        </Card>
      ) : (
        <div className="grid grid-cols-2 sm:grid-cols-4 gap-3 sm:gap-4 lg:gap-6">
          {dashboardStats.map((stat, index) => (
            <StatCard
              key={index}
              title={stat.title}
              value={stat.value}
              change={stat.change}
              icon={stat.icon}
              color={stat.color}
              loading={stat.title === "" ? teamLoading : statsLoading}
            />
          ))}
        </div>
      )}


      {/*   */}
      <div className="space-y-4 lg:space-y-6">
        <div>
          <h2 className="text-xl font-semibold mb-4 flex items-center">
            <Calendar className="w-5 h-5 mr-2 text-blue-600" />
             
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 lg:gap-6">
            {/*   */}
            <ScheduleSummary
              title=" "
              schedules={todaySchedules}
              loading={schedulesLoading}
              emptyMessage="   ."
              onViewAll={handleViewAllSchedules}
            />
            
            {/*   */}
            <ScheduleSummary
              title=" "
              schedules={upcomingSchedules}
              loading={schedulesLoading}
              emptyMessage="  ."
              onViewAll={handleViewAllSchedules}
            />
          </div>
        </div>
      </div>

      {/*    -    */}
      <div className="space-y-6">
        <div>
          <h2 className="text-xl font-semibold mb-4">  </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
            {properties
              ?.slice(0, 4)
              .map((property) => (
                <PropertyCard
                  key={property.id}
                  property={property}
                  onClick={handlePropertyClick}
                />
              )) ||
              recentProperties.map((property) => (
                <div
                  key={property.id}
                  className="card bg-white rounded-xl border border-gray-200 hover:border-blue-300 hover:shadow-md transition-all duration-200 cursor-pointer p-4 space-y-3"
                  onClick={() => {
                    /* Mock   Property    */
                  }}
                >
                  <h3 className="font-semibold text-lg line-clamp-1">
                    {property.title}
                  </h3>
                  <div className="flex items-center text-gray-600">
                    <span className="text-sm"></span>
                  </div>
                  <div className="flex items-center text-gray-900">
                    <span className="font-bold text-lg">{property.price}</span>
                  </div>
                  <div className="border-t border-gray-100 pt-3 flex items-center justify-between">
                    <div className="flex items-center space-x-2">
                      <span className="text-xs text-gray-500">
                        {property.type}
                      </span>
                      <span className="text-gray-300"></span>
                      <span className="text-xs text-gray-500">
                        {property.transactionType}
                      </span>
                    </div>
                    <div className="flex items-center">
                      <span className="inline-block w-2 h-2 rounded-full mr-2 bg-blue-400" />
                      <span className="text-xs font-medium text-blue-600">
                        
                      </span>
                    </div>
                  </div>
                </div>
              ))}
          </div>
        </div>
      </div>

      {/*   () */}
      {tenant && (
        <Card className="bg-gradient-to-r from-primary-50 to-blue-50 border-primary-200">
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <h3 className="text-lg font-semibold">
                  {tenant.plan ? (tenant.plan.charAt(0).toUpperCase() + tenant.plan.slice(1)) : ""}{" "}
                  
                </h3>
                <p className="text-gray-600 mt-1">
                   {tenant.limits.max_properties}  {" "}
                  {tenant.limits.max_users}  {" "}
                  {tenant.limits.max_storage_gb}GB
                </p>
              </div>
              <div className="text-right">
                <Badge variant="tenant" size="lg">
                  {tenant.status === "trial" ? " " : ""}
                </Badge>
                {tenant.trial_ends_at && (
                  <p className="text-xs text-gray-600 mt-1">
                     :{" "}
                    {new Date(tenant.trial_ends_at).toLocaleDateString()}
                  </p>
                )}
              </div>
            </div>
          </CardContent>
        </Card>
      )}

      {/*    */}
      <PropertyDetailModal
        property={selectedProperty}
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        onEdit={handleOpenEditForm}
        onDelete={handleConfirmDelete}
      />

      {/*     */}
      {editingProperty && (
        <PropertyEditForm
          isOpen={editFormOpen}
          onClose={() => {
            setEditFormOpen(false);
            setEditingProperty(null);
          }}
          onSubmit={handleEditProperty}
          property={editingProperty}
          loading={editLoading}
        />
      )}
    </div>
  );
};

export { DashboardPage };
