import React, { useState } from "react";
import { Download, FileText, AlertCircle } from "lucide-react";
import { Button, Modal, Loading, Select } from "@/components/ui";
import { Property } from "@/types";
import { useTenant } from "@/contexts/TenantContext";
import { useAuth } from "@/contexts/AuthContext";
import { useProperties } from "@/hooks/useProperties";
import {
  formatAsCSV,
  formatAsJSON,
} from "@/services/dataService";

interface DataExportModalProps {
  isOpen: boolean;
  onClose: () => void;
}

interface ExportOptions {
  format: "csv" | "excel" | "json";
  includeFields: string[];
  dateRange: "all" | "thisMonth" | "lastMonth" | "custom";
  customDateFrom?: string;
  customDateTo?: string;
  propertyStatus: "all" | "" | "";
  propertyType: "all" | "" | "" | "" | "" | "";
}

const DEFAULT_EXPORT_OPTIONS: ExportOptions = {
  format: "csv",
  includeFields: [
    "title",
    "type",
    "transaction_type",
    "status",
    "address",
    "area",
    "floor",
    "total_floors",
    "rooms",
    "bathrooms",
    "parking",
    "elevator",
    "price",
    "deposit",
    "monthly_rent",
    "landlord_name",
    "landlord_phone",
    "exit_date",
    "created_at",
  ],
  dateRange: "all",
  propertyStatus: "all",
  propertyType: "all",
};

const AVAILABLE_FIELDS = [
  { id: "title", label: "", required: true },
  { id: "type", label: "", required: true },
  { id: "transaction_type", label: "", required: true },
  { id: "status", label: "", required: false },
  { id: "address", label: "", required: true },
  { id: "detailed_address", label: "", required: false },
  { id: "area", label: "(m)", required: true },
  { id: "floor", label: "", required: true },
  { id: "total_floors", label: "", required: true },
  { id: "rooms", label: "", required: true },
  { id: "bathrooms", label: "", required: true },
  { id: "parking", label: "", required: false },
  { id: "elevator", label: "", required: false },
  { id: "price", label: "()", required: false },
  { id: "deposit", label: "()", required: false },
  { id: "monthly_rent", label: "()", required: false },
  { id: "maintenance_fee", label: "()", required: false },
  { id: "landlord_name", label: "", required: false },
  { id: "landlord_phone", label: " ", required: false },
  { id: "landlord_email", label: " ", required: false },
  { id: "exit_date", label: "", required: false },
  { id: "available_from", label: "", required: false },
  { id: "description", label: "", required: false },
  { id: "view_count", label: "", required: false },
  { id: "inquiry_count", label: "", required: false },
  { id: "created_at", label: "", required: false },
  { id: "updated_at", label: "", required: false },
];

export const DataExportModal: React.FC<DataExportModalProps> = ({
  isOpen,
  onClose,
}) => {
  const { user } = useAuth();

  //  user.id tenant.id 
  let tenant = user?.id ? { id: user.id, name: "PropertyDesk" } : null;
  let tenantLoading = false;

  // useTenant       
  try {
    const tenantContext = useTenant();
    if (tenantContext?.tenant?.id) {
      tenant = tenantContext.tenant;
      tenantLoading = tenantContext?.isLoading || false;
    }
  } catch (error) {
    console.log("TenantContext ,  ID  ID :", error);
    //   user.id    
  }

  const { properties } = useProperties();
  const [exportOptions, setExportOptions] = useState<ExportOptions>(
    DEFAULT_EXPORT_OPTIONS,
  );
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  //   
  console.log("DataExportModal -  :", {
    tenant: tenant?.id,
    tenantName: tenant?.name,
    tenantLoading,
    isOpen,
    userId: user?.id,
  });

  const updateExportOptions = (updates: Partial<ExportOptions>) => {
    setExportOptions((prev) => ({ ...prev, ...updates }));
  };

  const getFilteredProperties = (): Property[] => {
    //       
    //   handleExport   
    if (!properties) return [];

    return properties.filter((property) => {
      //   
      if (
        exportOptions.propertyStatus !== "all" &&
        property.status !== exportOptions.propertyStatus
      ) {
        return false;
      }

      //   
      if (
        exportOptions.propertyType !== "all" &&
        property.type !== exportOptions.propertyType
      ) {
        return false;
      }

      //   
      if (exportOptions.dateRange !== "all") {
        const createdAt = new Date(property.created_at);
        const today = new Date();

        switch (exportOptions.dateRange) {
          case "thisMonth":
            if (
              createdAt.getMonth() !== today.getMonth() ||
              createdAt.getFullYear() !== today.getFullYear()
            ) {
              return false;
            }
            break;

          case "lastMonth":
            const lastMonth = new Date(
              today.getFullYear(),
              today.getMonth() - 1,
            );
            if (
              createdAt.getMonth() !== lastMonth.getMonth() ||
              createdAt.getFullYear() !== lastMonth.getFullYear()
            ) {
              return false;
            }
            break;

          case "custom":
            if (
              exportOptions.customDateFrom &&
              createdAt < new Date(exportOptions.customDateFrom)
            ) {
              return false;
            }
            if (
              exportOptions.customDateTo &&
              createdAt > new Date(exportOptions.customDateTo + "T23:59:59")
            ) {
              return false;
            }
            break;
        }
      }

      return true;
    });
  };

  const downloadFile = (
    content: string,
    filename: string,
    mimeType: string,
  ) => {
    const blob = new Blob(["\ufeff" + content], {
      type: `${mimeType};charset=utf-8;`,
    });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = filename;
    link.click();
    URL.revokeObjectURL(link.href);
  };

  const handleExport = async () => {
    //    
    if (tenantLoading) {
      console.log("  .");
      return;
    }

    if (!tenant?.id) {
      console.log("  :", { tenant });
      setError(
        "  .     .",
      );
      return;
    }

    setLoading(true);
    setError(null);

    try {
      console.log(" :", {
        tenant: tenant?.id,
        options: exportOptions,
      });

      //       
      // (useProperties    )
      const filteredProperties = getFilteredProperties();
      console.log("  :", filteredProperties.length);

      if (filteredProperties.length === 0) {
        setError(
          "    .   .",
        );
        return;
      }

      const timestamp = new Date()
        .toISOString()
        .slice(0, 19)
        .replace(/[:-]/g, "")
        .replace("T", "_");
      let filename: string;
      let content: string;
      let mimeType: string;

      switch (exportOptions.format) {
        case "csv":
        case "excel":
          content = formatAsCSV(
            filteredProperties,
            exportOptions.includeFields,
          );
          filename = `_${timestamp}.csv`;
          mimeType = "text/csv";
          break;

        case "json":
          content = formatAsJSON(
            filteredProperties,
            exportOptions.includeFields,
          );
          filename = `_${timestamp}.json`;
          mimeType = "application/json";
          break;

        default:
          throw new Error("   .");
      }

      downloadFile(content, filename, mimeType);

      //    
      setTimeout(() => {
        onClose();
      }, 1000);
    } catch (err) {
      const message =
        err instanceof Error ? err.message : "   .";
      setError(message);
      console.error("Export error:", err);
    } finally {
      setLoading(false);
    }
  };

  const filteredProperties = getFilteredProperties();

  //       
  if (tenantLoading) {
    return (
      <Modal
        isOpen={isOpen}
        onClose={onClose}
        title=" "
        size="lg"
      >
        <div className="flex flex-col items-center justify-center py-12 space-y-4">
          <Loading size="lg" />
          <p className="text-gray-600">   ...</p>
        </div>
      </Modal>
    );
  }

  return (
    <Modal isOpen={isOpen} onClose={onClose} title=" " size="lg">
      <div className="space-y-6">
        <div className="text-center">
          <Download className="mx-auto h-12 w-12 text-blue-600 mb-4" />
          <h3 className="text-lg font-semibold text-gray-900 mb-2">
              
          </h3>
          <p className="text-gray-600">
                 
          </p>
        </div>

        <div className="space-y-6">
          {/*    */}
          <div className="space-y-3">
            <label className="block text-sm font-semibold text-gray-900">
               
            </label>
            <div className="grid grid-cols-3 gap-3">
              {[
                { value: "csv", label: "CSV", desc: "  " },
                { value: "json", label: "JSON", desc: "" },
                { value: "excel", label: "Excel", desc: " " },
              ].map((format) => (
                <button
                  key={format.value}
                  onClick={() =>
                    updateExportOptions({ format: format.value as any })
                  }
                  className={`p-3 border-2 rounded-lg text-center transition-all ${
                    exportOptions.format === format.value
                      ? "border-blue-500 bg-blue-50 text-blue-900"
                      : "border-gray-200 hover:border-gray-300"
                  }`}
                >
                  <div className="font-medium">{format.label}</div>
                  <div className="text-xs text-gray-500">{format.desc}</div>
                </button>
              ))}
            </div>
          </div>

          {/*    */}
          <div className="space-y-3">
            <label className="block text-sm font-semibold text-gray-900">
               
            </label>
            <Select
              value={exportOptions.dateRange}
              onChange={(e) =>
                updateExportOptions({ dateRange: e.target.value as any })
              }
              options={[
                { value: "all", label: " " },
                { value: "thisMonth", label: " " },
                { value: "lastMonth", label: " " },
                { value: "custom", label: " " },
              ]}
            />

            {exportOptions.dateRange === "custom" && (
              <div className="grid grid-cols-2 gap-3 mt-3">
                <div>
                  <label className="block text-xs font-medium text-gray-700 mb-1">
                    
                  </label>
                  <input
                    type="date"
                    value={exportOptions.customDateFrom || ""}
                    onChange={(e) =>
                      updateExportOptions({ customDateFrom: e.target.value })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  />
                </div>
                <div>
                  <label className="block text-xs font-medium text-gray-700 mb-1">
                    
                  </label>
                  <input
                    type="date"
                    value={exportOptions.customDateTo || ""}
                    onChange={(e) =>
                      updateExportOptions({ customDateTo: e.target.value })
                    }
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  />
                </div>
              </div>
            )}
          </div>

          {/*   */}
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <label className="block text-sm font-semibold text-gray-900">
                 
              </label>
              <Select
                value={exportOptions.propertyStatus}
                onChange={(e) =>
                  updateExportOptions({ propertyStatus: e.target.value as any })
                }
                options={[
                  { value: "all", label: "" },
                  { value: "", label: "" },
                  { value: "", label: "" },
                ]}
              />
            </div>

            <div className="space-y-2">
              <label className="block text-sm font-semibold text-gray-900">
                 
              </label>
              <Select
                value={exportOptions.propertyType}
                onChange={(e) =>
                  updateExportOptions({ propertyType: e.target.value as any })
                }
                options={[
                  { value: "all", label: "" },
                  { value: "", label: "" },
                  { value: "", label: "" },
                  { value: "", label: "" },
                  { value: "", label: "" },
                  { value: "", label: "" },
                ]}
              />
            </div>
          </div>

          {/*    */}
          <div className="space-y-3">
            <label className="block text-sm font-semibold text-gray-900">
                
            </label>
            <div className="bg-gray-50 rounded-lg p-4 max-h-48 overflow-y-auto">
              <div className="grid grid-cols-2 gap-2">
                {AVAILABLE_FIELDS.map((field) => (
                  <label
                    key={field.id}
                    className="flex items-center space-x-2 cursor-pointer"
                  >
                    <input
                      type="checkbox"
                      checked={exportOptions.includeFields.includes(field.id)}
                      disabled={field.required}
                      onChange={(e) => {
                        if (e.target.checked) {
                          updateExportOptions({
                            includeFields: [
                              ...exportOptions.includeFields,
                              field.id,
                            ],
                          });
                        } else {
                          updateExportOptions({
                            includeFields: exportOptions.includeFields.filter(
                              (id) => id !== field.id,
                            ),
                          });
                        }
                      }}
                      className="rounded text-blue-600"
                    />
                    <span
                      className={`text-sm ${field.required ? "font-medium text-gray-900" : "text-gray-700"}`}
                    >
                      {field.label}
                      {field.required && (
                        <span className="text-red-500 ml-1">*</span>
                      )}
                    </span>
                  </label>
                ))}
              </div>
            </div>
            <p className="text-xs text-gray-500">
              *     
            </p>
          </div>

          {/*   */}
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
            <div className="flex items-start space-x-3">
              <FileText className="h-5 w-5 text-blue-600 mt-0.5" />
              <div>
                <p className="font-medium text-blue-900"> </p>
                <div className="text-sm text-blue-800 mt-1 space-y-1">
                  <p>   : {filteredProperties.length}</p>
                  <p>  : {exportOptions.includeFields.length}</p>
                  <p>  : {exportOptions.format.toUpperCase()}</p>
                </div>
              </div>
            </div>
          </div>

          {error && (
            <div className="bg-red-50 border border-red-200 rounded-lg p-4">
              <div className="flex items-start space-x-3">
                <AlertCircle className="h-5 w-5 text-red-600 mt-0.5" />
                <div>
                  <p className="font-medium text-red-900"></p>
                  <p className="text-sm text-red-600">{error}</p>
                </div>
              </div>
            </div>
          )}
        </div>

        <div className="flex justify-between space-x-3">
          <Button variant="outline" onClick={onClose}>
            
          </Button>
          <Button
            onClick={handleExport}
            disabled={
              loading ||
              tenantLoading ||
              !tenant?.id ||
              filteredProperties.length === 0
            }
            leftIcon={loading ? <Loading size="sm" /> : <Download size={18} />}
          >
            {loading
              ? " ..."
              : tenantLoading
                ? "   ..."
                : !tenant?.id
                  ? "  "
                  : `${filteredProperties.length}  `}
          </Button>
        </div>
      </div>
    </Modal>
  );
};
