import { supabase } from "./supabase";
import { Property, CreatePropertyData, PropertyStatus } from "@/types";

export interface ImportResult {
  success: number;
  failed: number;
  errors: ImportError[];
  importedProperties: Property[];
}

export interface ImportError {
  row: number;
  field?: string;
  message: string;
  data?: CreatePropertyData;
}

export interface ExportOptions {
  format: "csv" | "excel" | "json";
  includeFields: string[];
  filters?: {
    dateRange?: "all" | "thisMonth" | "lastMonth" | "custom";
    customDateFrom?: string;
    customDateTo?: string;
    propertyStatus?: "all" | PropertyStatus;
    propertyType?: string;
  };
}

/**
 *   
 */
export const importProperties = async (
  properties: CreatePropertyData[],
  tenantId: string,
  createdBy: string,
): Promise<ImportResult> => {
  const errors: ImportError[] = [];
  const importedProperties: Property[] = [];

  for (let i = 0; i < properties.length; i++) {
    const property = properties[i];

    try {
      //  
      const validationError = validatePropertyData(property, i + 1);
      if (validationError) {
        errors.push(validationError);
        continue;
      }

      // Supabase   
      const { data, error } = await supabase
        .from("properties")
        .insert({
          tenant_id: tenantId,
          created_by: createdBy,
          ...property,
          view_count: 0,
          inquiry_count: 0,
          is_featured: false,
          is_urgent: false,
          is_favorite: false,
        })
        .select()
        .single();

      if (error) {
        errors.push({
          row: i + 1,
          message: ` : ${error.message}`,
          data: property,
        });
        continue;
      }

      if (data) {
        importedProperties.push(data as Property);
      }
    } catch (err) {
      errors.push({
        row: i + 1,
        message: `  : ${err instanceof Error ? err.message : "   "}`,
        data: property,
      });
    }
  }

  return {
    success: importedProperties.length,
    failed: properties.length - importedProperties.length,
    errors,
    importedProperties,
  };
};

/**
 *   
 */
export const exportProperties = async (
  tenantId: string,
  options: ExportOptions,
): Promise<Property[]> => {
  console.log("  :", { tenantId, options });

  let query = supabase.from("properties").select("*").eq("tenant_id", tenantId);

  //     
  const { data: allProperties } = await supabase
    .from("properties")
    .select("*")
    .eq("tenant_id", tenantId);

  console.log("   :", allProperties?.length || 0);
  console.log("   :", allProperties?.slice(0, 3));

  //  
  if (options.filters) {
    const { filters } = options;
    console.log("  :", filters);

    //   
    if (filters.propertyStatus && filters.propertyStatus !== "all") {
      console.log("   :", filters.propertyStatus);
      query = query.eq("status", filters.propertyStatus);
    }

    //   
    if (filters.propertyType && filters.propertyType !== "all") {
      console.log("   :", filters.propertyType);
      query = query.eq("type", filters.propertyType);
    }

    //   
    if (filters.dateRange && filters.dateRange !== "all") {
      const now = new Date();
      console.log("   :", filters.dateRange);

      switch (filters.dateRange) {
        case "thisMonth":
          const thisMonthStart = new Date(now.getFullYear(), now.getMonth(), 1);
          console.log("   :", thisMonthStart.toISOString());
          query = query.gte("created_at", thisMonthStart.toISOString());
          break;

        case "lastMonth":
          const lastMonthStart = new Date(
            now.getFullYear(),
            now.getMonth() - 1,
            1,
          );
          const lastMonthEnd = new Date(now.getFullYear(), now.getMonth(), 0);
          console.log("  :", lastMonthStart.toISOString(), "~", lastMonthEnd.toISOString());
          query = query
            .gte("created_at", lastMonthStart.toISOString())
            .lte("created_at", lastMonthEnd.toISOString());
          break;

        case "custom":
          if (filters.customDateFrom) {
            console.log("   :", filters.customDateFrom);
            query = query.gte("created_at", filters.customDateFrom);
          }
          if (filters.customDateTo) {
            const endDate = filters.customDateTo + "T23:59:59";
            console.log("   :", endDate);
            query = query.lte("created_at", endDate);
          }
          break;
      }
    }
  }

  //    
  query = query.order("created_at", { ascending: false });

  const { data, error } = await query;

  console.log("   :", data?.length || 0);
  console.log("   :", data?.slice(0, 3));

  if (error) {
    console.error("  :", error);
    throw new Error(`  : ${error.message}`);
  }

  return data || [];
};

/**
 * CSV   
 */
export const formatAsCSV = (
  properties: Property[],
  includeFields: string[],
): string => {
  const fieldLabels: Record<string, string> = {
    title: "",
    type: "",
    transaction_type: "",
    status: "",
    address: "",
    detailed_address: "",
    area: "(m)",
    floor: "",
    total_floors: "",
    rooms: "",
    bathrooms: "",
    parking: "",
    elevator: "",
    price: "()",
    deposit: "()",
    monthly_rent: "()",
    maintenance_fee: "()",
    landlord_name: "",
    landlord_phone: " ",
    landlord_email: " ",
    exit_date: "",
    available_from: "",
    description: "",
    view_count: "",
    inquiry_count: "",
    created_at: "",
    updated_at: "",
  };

  //  
  const headers = includeFields.map((field) => fieldLabels[field] || field);

  //   
  const rows = properties.map((property) => {
    return includeFields.map((field) => {
      const value = formatFieldValue(property, field);
      // CSV  
      return `"${String(value).replace(/"/g, '""')}"`;
    });
  });

  // CSV  
  return [headers, ...rows].map((row) => row.join(",")).join("\n");
};

/**
 * JSON   
 */
export const formatAsJSON = (
  properties: Property[],
  includeFields: string[],
): string => {
  const fieldLabels: Record<string, string> = {
    title: "",
    type: "",
    transaction_type: "",
    status: "",
    address: "",
    detailed_address: "",
    area: "",
    floor: "",
    total_floors: "",
    rooms: "",
    bathrooms: "",
    parking: "",
    elevator: "",
    price: "",
    deposit: "",
    monthly_rent: "",
    maintenance_fee: "",
    landlord_name: "",
    landlord_phone: " ",
    landlord_email: " ",
    exit_date: "",
    available_from: "",
    description: "",
    view_count: "",
    inquiry_count: "",
    created_at: "",
    updated_at: "",
  };

  interface ExportItem {
    [key: string]: string | number | boolean | Date;
  }

  const exportData = properties.map((property) => {
    const exportItem: ExportItem = {};
    includeFields.forEach((field) => {
      const label = fieldLabels[field] || field;
      exportItem[label] = formatFieldValue(property, field);
    });
    return exportItem;
  });

  return JSON.stringify(
    {
      exportDate: new Date().toISOString(),
      totalRecords: exportData.length,
      data: exportData,
    },
    null,
    2,
  );
};

/**
 *   
 */
const formatFieldValue = (
  property: Property,
  fieldId: string,
): string | number | boolean => {
  const value = property[fieldId as keyof Property];

  if (value === null || value === undefined) {
    return "";
  }

  switch (fieldId) {
    case "parking":
    case "elevator":
      return typeof value === "boolean" ? (value ? "" : "") : "";

    case "price":
    case "deposit":
    case "monthly_rent":
    case "maintenance_fee":
      return typeof value === "number" ? value.toLocaleString() : "";

    case "created_at":
    case "updated_at":
    case "exit_date":
    case "available_from":
      return typeof value === "string"
        ? new Date(value).toLocaleDateString("ko-KR")
        : "";

    default:
      //  (, )  
      if (typeof value === "object" && value !== null) {
        return Array.isArray(value) ? value.join(", ") : JSON.stringify(value);
      }
      return typeof value === "string" ||
        typeof value === "number" ||
        typeof value === "boolean"
        ? value
        : String(value);
  }
};

/**
 *   
 */
const validatePropertyData = (
  data: CreatePropertyData,
  row: number,
): ImportError | null => {
  const requiredFields = [
    "title",
    "type",
    "transaction_type",
    "address",
    "area",
    "floor",
    "total_floors",
    "rooms",
    "bathrooms",
  ];

  for (const field of requiredFields) {
    const value = data[field as keyof CreatePropertyData];
    if (value === undefined || value === null || value === "") {
      return {
        row,
        field,
        message: `  '${field}' .`,
      };
    }
  }

  //   
  const numericFields: (keyof CreatePropertyData)[] = [
    "area",
    "floor",
    "total_floors",
    "rooms",
    "bathrooms",
    "price",
    "deposit",
    "monthly_rent",
  ];
  for (const field of numericFields) {
    const value = data[field];
    if (value !== undefined && value !== null && value !== "") {
      if (typeof value !== "number" || isNaN(value) || value < 0) {
        return {
          row,
          field,
          message: `'${field}'   .`,
        };
      }
    }
  }

  //   
  const validPropertyTypes = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  if (!validPropertyTypes.includes(data.type)) {
    return {
      row,
      field: "type",
      message: `     : ${validPropertyTypes.join(", ")}`,
    };
  }

  //   
  const validTransactionTypes = ["", "", "", ""];
  if (!validTransactionTypes.includes(data.transaction_type)) {
    return {
      row,
      field: "transaction_type",
      message: `     : ${validTransactionTypes.join(", ")}`,
    };
  }

  //   
  if (data.exit_date) {
    const date = new Date(data.exit_date);
    if (isNaN(date.getTime())) {
      return {
        row,
        field: "exit_date",
        message: `    . (: 2024-12-31)`,
      };
    }
  }

  return null;
};

/**
 *   
 */
export const validateFileSize = (
  file: File,
  maxSizeMB: number = 10,
): boolean => {
  const maxSizeBytes = maxSizeMB * 1024 * 1024;
  return file.size <= maxSizeBytes;
};

/**
 *   
 */
export const validateFileFormat = (file: File): boolean => {
  const allowedTypes = [
    "text/csv",
    "application/vnd.ms-excel",
    "text/tab-separated-values",
  ];
  const allowedExtensions = [".csv", ".tsv", ".txt"];

  return (
    allowedTypes.includes(file.type) ||
    allowedExtensions.some((ext) => file.name.toLowerCase().endsWith(ext))
  );
};

