import React, {
  createContext,
  useContext,
  useEffect,
  useState,
  ReactNode,
} from "react";
import { useAuth } from "@/contexts/AuthContext";
import { supabase } from "@/services/supabase";
import type { TenantContextType, TenantStats } from "@/types/tenant";

const TenantContext = createContext<TenantContextType | null>(null);

interface TenantProviderProps {
  children: ReactNode;
}

export const TenantProvider: React.FC<TenantProviderProps> = ({ children }) => {
  const { user } = useAuth(); //    

  // Local state management instead of Zustand
  const [tenant, setTenant] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Tenant management functions
  const switchTenant = async (tenantId: string) => {
    setIsLoading(true);
    try {
      const { data, error } = await supabase
        .from("tenants")
        .select("*")
        .eq("id", tenantId)
        .single();

      if (error) throw error;
      setTenant(data);
      setError(null);
    } catch (err) {
      console.error("Failed to switch tenant:", err);
      setError("  .");
    } finally {
      setIsLoading(false);
    }
  };

  const updateTenant = async (updates: any) => {
    if (!tenant) return;

    setIsLoading(true);
    try {
      const { data, error } = await supabase
        .from("tenants")
        .update(updates)
        .eq("id", tenant.id)
        .select()
        .single();

      if (error) throw error;
      setTenant(data);
      setError(null);
    } catch (err) {
      console.error("Failed to update tenant:", err);
      setError("  .");
    } finally {
      setIsLoading(false);
    }
  };

  const getTenantStats = async (): Promise<TenantStats> => {
    if (!tenant) {
      // Return default stats if no tenant
      return {
        total_properties: 0,
        active_properties: 0,
        total_users: 0,
        active_users: 0,
        storage_used_mb: 0,
        api_calls_this_month: 0,
        created_this_month: 0,
      };
    }

    try {
      const { data, error } = await supabase
        .from("properties")
        .select("id, status, created_at")
        .eq("tenant_id", tenant.id);

      if (error) throw error;

      const stats: TenantStats = {
        total_properties: data.length,
        active_properties: data.length, //     ( )
        total_users: 1, // Simplified - would need actual user count
        active_users: 1, // Simplified - would need actual active user count
        storage_used_mb: 0, // Simplified - would need actual storage calculation
        api_calls_this_month: 0, // Simplified - would need actual API call tracking
        created_this_month: data.filter((p) => {
          const created = new Date(p.created_at || Date.now());
          const now = new Date();
          return (
            created.getMonth() === now.getMonth() &&
            created.getFullYear() === now.getFullYear()
          );
        }).length,
      };

      return stats;
    } catch (err) {
      console.error("Failed to get tenant stats:", err);
      // Return default stats on error
      return {
        total_properties: 0,
        active_properties: 0,
        total_users: 0,
        active_users: 0,
        storage_used_mb: 0,
        api_calls_this_month: 0,
        created_this_month: 0,
      };
    }
  };

  const hasFeature = (feature: string) => {
    return tenant?.limits?.features_enabled?.includes(feature) || false;
  };

  const canCreateProperty = () => {
    if (!tenant?.limits) return true;
    // Simplified - would need actual property count check
    return true;
  };

  const canInviteUser = () => {
    if (!tenant?.limits) return true;
    // Simplified - would need actual user count check
    return true;
  };

  const isWithinLimits = (_resource: string) => {
    if (!tenant?.limits) return true;
    // Simplified - would need actual resource usage check
    return true;
  };

  //    URL      
  useEffect(() => {
    console.log(" TenantContext useEffect :", {
      user: user?.id,
      tenant: tenant?.id,
    });

    //     
    if (user && !tenant) {
      console.log("     :", user.id);

      //     DB /
      const initializeTenant = async () => {
        setIsLoading(true);
        
        //     (  )
        if (!import.meta.env.VITE_SUPABASE_URL) {
          console.log(" :    ");
          const mockTenant = {
            id: user.id,
            name: `${user.name || ""} `,
            slug: `user-${user.id}`,
            plan: "starter" as const,
            status: "active" as const,
            branding: {
              primary_color: "#3b82f6",
              secondary_color: "#1d4ed8",
              accent_color: "#f59e0b",
            },
            limits: {
              max_properties: 50,
              max_users: 2,
              max_storage_gb: 1,
              max_api_calls_per_month: 1000,
              features_enabled: ["basic"],
            },
            settings: {
              timezone: "Asia/Seoul",
              date_format: "YYYY-MM-DD",
              currency: "KRW",
              language: "ko",
              require_exit_date: true,
              require_landlord_info: true,
              email_notifications: true,
              sms_notifications: false,
              browser_notifications: true,
              require_2fa: false,
              session_timeout_minutes: 480,
            },
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            created_by: user.id,
          };
          setTenant(mockTenant);
          setError(null);
          setIsLoading(false);
          return;
        }

        try {
          // 1.    ( )
          console.log("    :", user.id);
          const { data: existingTenant, error: queryError } = await supabase
            .from("tenants")
            .select("*")
            .eq("id", user.id)
            .single();

          console.log("  :", { existingTenant, queryError });

          if (existingTenant && !queryError) {
            //      (  )
            const fullTenant = {
              ...existingTenant,
              branding: {
                primary_color: "#3b82f6",
                secondary_color: "#1d4ed8",
                accent_color: "#f59e0b",
              },
              limits: {
                max_properties: 50,
                max_users: 2,
                max_storage_gb: 1,
                max_api_calls_per_month: 1000,
                features_enabled: ["basic"],
              },
              settings: {
                timezone: "Asia/Seoul",
                date_format: "YYYY-MM-DD",
                currency: "KRW",
                language: "ko",
                require_exit_date: true,
                require_landlord_info: true,
                email_notifications: true,
                sms_notifications: false,
                browser_notifications: true,
                require_2fa: false,
                session_timeout_minutes: 480,
              },
            };
            setTenant(fullTenant);
            setError(null);
            console.log("    :", existingTenant.name);
            setIsLoading(false);
            return;
          }
        } catch (error) {
          console.log("     :", error);
          //     fallback
          console.log("      ");
          const mockTenant = {
            id: user.id,
            name: `${user.name || ""} `,
            slug: `user-${user.id}`,
            plan: "starter" as const,
            status: "active" as const,
            branding: {
              primary_color: "#3b82f6",
              secondary_color: "#1d4ed8",
              accent_color: "#f59e0b",
            },
            limits: {
              max_properties: 50,
              max_users: 2,
              max_storage_gb: 1,
              max_api_calls_per_month: 1000,
              features_enabled: ["basic"],
            },
            settings: {
              timezone: "Asia/Seoul",
              date_format: "YYYY-MM-DD",
              currency: "KRW",
              language: "ko",
              require_exit_date: true,
              require_landlord_info: true,
              email_notifications: true,
              sms_notifications: false,
              browser_notifications: true,
              require_2fa: false,
              session_timeout_minutes: 480,
            },
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            created_by: user.id,
          };
          setTenant(mockTenant);
          setError(null);
          setIsLoading(false);
          return;
        }

        //    
        console.log("    ");

        try {
          // 2.   
          const userTenant = {
            id: user.id, //  ID =  ID
            name: "PropertyDesk ",
          };

          console.log("   :", userTenant);
          const { data: newTenant, error: createError } = await supabase
            .from("tenants")
            .insert(userTenant)
            .select()
            .single();

          console.log("   :", { newTenant, createError });

          if (createError) {
            console.error("   :", createError);
            throw createError;
          }

          //     (  )
          const fullTenant = {
            ...newTenant,
            branding: {
              primary_color: "#3b82f6",
              secondary_color: "#1d4ed8",
              accent_color: "#f59e0b",
            },
            limits: {
              max_properties: 1000,
              max_users: 10,
              max_storage_gb: 10,
              max_api_calls_per_month: 50000,
              features_enabled: [
                "advanced_analytics",
                "api_access",
                "custom_fields",
              ],
            },
            settings: {
              timezone: "Asia/Seoul",
              date_format: "YYYY-MM-DD",
              currency: "KRW",
              language: "ko",
              // default_property_status:    
              require_exit_date: true,
              require_landlord_info: true,
              email_notifications: true,
              sms_notifications: false,
              browser_notifications: true,
              require_2fa: false,
              session_timeout_minutes: 480,
            },
          };

          setTenant(fullTenant);
          setError(null);

          console.log(
            "    :",
            newTenant.name,
            "User ID:",
            user.id,
          );
        } catch (error) {
          console.error("   :", error);
          setTenant(null);
          setError("  .");
        } finally {
          setIsLoading(false);
        }
      };

      initializeTenant();
    }
  }, [user, tenant]); // user  

  //  CSS   ( )
  useEffect(() => {
    if (tenant?.branding) {
      const root = document.documentElement;
      root.style.setProperty("--tenant-primary", tenant.branding.primary_color);
      root.style.setProperty(
        "--tenant-secondary",
        tenant.branding.secondary_color || "#6b7280",
      );
      root.style.setProperty(
        "--tenant-accent",
        tenant.branding.accent_color || "#f59e0b",
      );

      //  
      if (tenant.branding.favicon_url) {
        const favicon = document.querySelector(
          'link[rel="icon"]',
        ) as HTMLLinkElement;
        if (favicon) {
          favicon.href = tenant.branding.favicon_url;
        }
      }

      //   
      document.title = `${tenant.name} - PropertyDesk`;
    } else {
      //   
      const root = document.documentElement;
      root.style.setProperty("--tenant-primary", "#3b82f6");
      root.style.setProperty("--tenant-secondary", "#6b7280");
      root.style.setProperty("--tenant-accent", "#f59e0b");
      document.title = "PropertyDesk -   ";
    }
  }, [tenant]);

  const contextValue: TenantContextType = {
    tenant,
    isLoading,
    error,
    switchTenant,
    updateTenant,
    getTenantStats,
    hasFeature,
    canCreateProperty,
    canInviteUser,
    isWithinLimits,
  };

  return (
    <TenantContext.Provider value={contextValue}>
      {children}
    </TenantContext.Provider>
  );
};

//   
export const useTenant = () => {
  const context = useContext(TenantContext);
  if (!context) {
    throw new Error("useTenant TenantProvider   .");
  }
  return context;
};

//    
interface TenantGuardProps {
  children: ReactNode;
  fallback?: ReactNode;
  requireFeatures?: string[];
}

export const TenantGuard: React.FC<TenantGuardProps> = ({
  children,
  fallback = <div> .</div>,
  requireFeatures = [],
}) => {
  const { tenant, hasFeature } = useTenant();

  //   
  if (!tenant) {
    return <>{fallback}</>;
  }

  //    
  if (requireFeatures.length > 0) {
    const missingFeatures = requireFeatures.filter(
      (feature) => !hasFeature(feature),
    );
    if (missingFeatures.length > 0) {
      return (
        <div className="p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
          <p className="text-yellow-800">
                 :{" "}
            {missingFeatures.join(", ")}
          </p>
          <p className="text-sm text-yellow-600 mt-1">
               .
          </p>
        </div>
      );
    }
  }

  return <>{children}</>;
};

//   
export const getTenantUrl = (
  path: string,
  tenant?: { slug: string } | null,
) => {
  if (!tenant) return path;

  //  
  const isProduction = window.location.hostname.includes("propertydesk.com");
  if (isProduction) {
    return `https://${tenant.slug}.propertydesk.com${path}`;
  }

  //  :  
  return `/tenant/${tenant.slug}${path}`;
};

//   
export const useTenantSettings = () => {
  const { tenant, updateTenant } = useTenant();

  const updateSettings = async (settings: any) => {
    if (!tenant) return;

    await updateTenant({
      settings: {
        ...tenant.settings,
        ...settings,
      },
    });
  };

  return {
    settings: tenant?.settings,
    updateSettings,
    timezone: tenant?.settings.timezone || "Asia/Seoul",
    currency: tenant?.settings.currency || "KRW",
    language: tenant?.settings.language || "ko",
  };
};

//   
export const useTenantBranding = () => {
  const { tenant, updateTenant } = useTenant();

  const updateBranding = async (branding: any) => {
    if (!tenant) return;

    await updateTenant({
      branding: {
        ...tenant.branding,
        ...branding,
      },
    });
  };

  return {
    branding: tenant?.branding,
    updateBranding,
    primaryColor: tenant?.branding.primary_color || "#3b82f6",
    logoUrl: tenant?.branding.logo_url,
  };
};
