import React, { useState } from "react";
import {
  X,
  User,
  Mail,
  Phone,
  Building,
  Crown,
  Shield,
  Eye,
} from "lucide-react";
import type { TeamMember } from "@/types/team";
import { ROLE_LABELS } from "@/types/team";

interface MemberEditModalProps {
  member: TeamMember;
  isOpen: boolean;
  onClose: () => void;
  onUpdate: (memberId: string, data: any) => Promise<void>;
  onRoleChange: (
    memberId: string,
    newRole: TeamMember["role"],
  ) => Promise<void>;
  onStatusChange: (
    memberId: string,
    newStatus: TeamMember["status"],
  ) => Promise<void>;
  currentUserRole: string;
  canEdit: boolean;
}

export const MemberEditModal: React.FC<MemberEditModalProps> = ({
  member,
  isOpen,
  onClose,
  onUpdate,
  onRoleChange,
  onStatusChange,
  currentUserRole,
  canEdit,
}) => {
  const [activeTab, setActiveTab] = useState<"info" | "role" | "status">(
    "info",
  );
  const [loading, setLoading] = useState(false);

  //   
  const [editData, setEditData] = useState({
    name: member.name || "",
    email: member.email || "",
    phone: member.phone || "",
    company: member.company || "",
  });

  if (!isOpen) return null;

  const handleInfoUpdate = async () => {
    try {
      setLoading(true);
      await onUpdate(member.id, editData);
      onClose();
    } catch (error: any) {
      alert(error.message || "  .");
    } finally {
      setLoading(false);
    }
  };

  const handleRoleChange = async (newRole: TeamMember["role"]) => {
    if (
      !confirm(
        `${member.name}  ${ROLE_LABELS[newRole]} ?`,
      )
    )
      return;

    try {
      setLoading(true);
      await onRoleChange(member.id, newRole);
      onClose();
    } catch (error: any) {
      alert(error.message || "  .");
    } finally {
      setLoading(false);
    }
  };

  const handleStatusChange = async (newStatus: TeamMember["status"]) => {
    if (!confirm(`${member.name}  ?`)) return;

    try {
      setLoading(true);
      await onStatusChange(member.id, newStatus);
      onClose();
    } catch (error: any) {
      alert(error.message || "  .");
    } finally {
      setLoading(false);
    }
  };

  const getRoleIcon = (role: string) => {
    switch (role) {
      case "owner":
        return <Crown className="w-4 h-4 text-yellow-500" />;
      case "admin":
        return <Shield className="w-4 h-4 text-blue-500" />;
      case "member":
        return <User className="w-4 h-4 text-green-500" />;
      case "viewer":
        return <Eye className="w-4 h-4 text-gray-500" />;
      default:
        return <User className="w-4 h-4" />;
    }
  };

  const canChangeRole = (targetRole: string) => {
    if (currentUserRole === "owner") return true;
    if (
      currentUserRole === "admin" &&
      ["member", "viewer"].includes(targetRole)
    )
      return true;
    return false;
  };

  const canChangeStatus =
    currentUserRole === "owner" || currentUserRole === "admin";

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg w-full max-w-2xl mx-4 max-h-[90vh] overflow-hidden">
        {/*  */}
        <div className="border-b border-gray-200 px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center">
                <User className="w-5 h-5 text-blue-600" />
              </div>
              <div>
                <h2 className="text-lg font-semibold">{member.name}</h2>
                <div className="flex items-center space-x-2">
                  {getRoleIcon(member.role)}
                  <span className="text-sm text-gray-600">
                    {ROLE_LABELS[member.role]}
                  </span>
                </div>
              </div>
            </div>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/*   */}
        <div className="border-b border-gray-200">
          <nav className="flex">
            <button
              onClick={() => setActiveTab("info")}
              className={`px-6 py-3 text-sm font-medium ${
                activeTab === "info"
                  ? "border-b-2 border-blue-500 text-blue-600"
                  : "text-gray-500 hover:text-gray-700"
              }`}
            >
               
            </button>
            {canEdit && (
              <>
                <button
                  onClick={() => setActiveTab("role")}
                  className={`px-6 py-3 text-sm font-medium ${
                    activeTab === "role"
                      ? "border-b-2 border-blue-500 text-blue-600"
                      : "text-gray-500 hover:text-gray-700"
                  }`}
                >
                   
                </button>
                <button
                  onClick={() => setActiveTab("status")}
                  className={`px-6 py-3 text-sm font-medium ${
                    activeTab === "status"
                      ? "border-b-2 border-blue-500 text-blue-600"
                      : "text-gray-500 hover:text-gray-700"
                  }`}
                >
                   
                </button>
              </>
            )}
          </nav>
        </div>

        {/*   */}
        <div className="p-6 overflow-y-auto max-h-[60vh]">
          {/*    */}
          {activeTab === "info" && (
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="relative">
                  <User className="w-4 h-4 absolute left-3 top-3 text-gray-400" />
                  <input
                    type="text"
                    value={editData.name}
                    onChange={(e) =>
                      setEditData({ ...editData, name: e.target.value })
                    }
                    className="w-full pl-10 p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    placeholder=" "
                    readOnly={!canEdit}
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="relative">
                  <Mail className="w-4 h-4 absolute left-3 top-3 text-gray-400" />
                  <input
                    type="email"
                    value={editData.email}
                    onChange={(e) =>
                      setEditData({ ...editData, email: e.target.value })
                    }
                    className="w-full pl-10 p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    placeholder=" "
                    readOnly={!canEdit}
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="relative">
                  <Phone className="w-4 h-4 absolute left-3 top-3 text-gray-400" />
                  <input
                    type="tel"
                    value={editData.phone}
                    onChange={(e) =>
                      setEditData({ ...editData, phone: e.target.value })
                    }
                    className="w-full pl-10 p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    placeholder=" "
                    readOnly={!canEdit}
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="relative">
                  <Building className="w-4 h-4 absolute left-3 top-3 text-gray-400" />
                  <input
                    type="text"
                    value={editData.company}
                    onChange={(e) =>
                      setEditData({ ...editData, company: e.target.value })
                    }
                    className="w-full pl-10 p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    placeholder=" "
                    readOnly={!canEdit}
                  />
                </div>
              </div>

              <div className="text-sm text-gray-500">
                <p>
                  :{" "}
                  {new Date(member.joined_at).toLocaleDateString("ko-KR")}
                </p>
              </div>
            </div>
          )}

          {/*    */}
          {activeTab === "role" && canEdit && (
            <div className="space-y-4">
              <p className="text-sm text-gray-600">
                 :{" "}
                <span className="font-medium">{ROLE_LABELS[member.role]}</span>
              </p>

              <div className="grid grid-cols-1 gap-3">
                {(["viewer", "member", "admin", "owner"] as const).map(
                  (role) => (
                    <button
                      key={role}
                      onClick={() => handleRoleChange(role)}
                      disabled={
                        !canChangeRole(role) || member.role === role || loading
                      }
                      className={`p-4 border rounded-lg text-left transition-colors ${
                        member.role === role
                          ? "border-blue-500 bg-blue-50"
                          : canChangeRole(role)
                            ? "border-gray-200 hover:border-gray-300 hover:bg-gray-50"
                            : "border-gray-100 bg-gray-50 text-gray-400 cursor-not-allowed"
                      }`}
                    >
                      <div className="flex items-center space-x-3">
                        {getRoleIcon(role)}
                        <div>
                          <div className="font-medium">{ROLE_LABELS[role]}</div>
                          <div className="text-sm text-gray-500">
                            {role === "owner" &&
                              "      ."}
                            {role === "admin" &&
                              "     ."}
                            {role === "member" &&
                              "    ."}
                            {role === "viewer" &&
                              "    ."}
                          </div>
                        </div>
                      </div>
                    </button>
                  ),
                )}
              </div>
            </div>
          )}

          {/*    */}
          {activeTab === "status" && canEdit && canChangeStatus && (
            <div className="space-y-4">
              <p className="text-sm text-gray-600">
                 :{" "}
                <span className="font-medium">
                  {member.status === "active"
                    ? ""
                    : member.status === "inactive"
                      ? ""
                      : ""}
                </span>
              </p>

              <div className="grid grid-cols-1 gap-3">
                <button
                  onClick={() => handleStatusChange("active")}
                  disabled={member.status === "active" || loading}
                  className={`p-4 border rounded-lg text-left transition-colors ${
                    member.status === "active"
                      ? "border-green-500 bg-green-50"
                      : "border-gray-200 hover:border-gray-300 hover:bg-gray-50"
                  }`}
                >
                  <div className="flex items-center space-x-3">
                    <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                    <div>
                      <div className="font-medium"></div>
                      <div className="text-sm text-gray-500">
                            .
                      </div>
                    </div>
                  </div>
                </button>

                <button
                  onClick={() => handleStatusChange("inactive")}
                  disabled={member.status === "inactive" || loading}
                  className={`p-4 border rounded-lg text-left transition-colors ${
                    member.status === "inactive"
                      ? "border-gray-500 bg-gray-50"
                      : "border-gray-200 hover:border-gray-300 hover:bg-gray-50"
                  }`}
                >
                  <div className="flex items-center space-x-3">
                    <div className="w-3 h-3 bg-gray-500 rounded-full"></div>
                    <div>
                      <div className="font-medium"></div>
                      <div className="text-sm text-gray-500">
                           .
                      </div>
                    </div>
                  </div>
                </button>

                <button
                  onClick={() => handleStatusChange("suspended")}
                  disabled={member.status === "suspended" || loading}
                  className={`p-4 border rounded-lg text-left transition-colors ${
                    member.status === "suspended"
                      ? "border-red-500 bg-red-50"
                      : "border-gray-200 hover:border-gray-300 hover:bg-gray-50"
                  }`}
                >
                  <div className="flex items-center space-x-3">
                    <div className="w-3 h-3 bg-red-500 rounded-full"></div>
                    <div>
                      <div className="font-medium"></div>
                      <div className="text-sm text-gray-500">
                          .
                      </div>
                    </div>
                  </div>
                </button>
              </div>
            </div>
          )}
        </div>

        {/*   */}
        {activeTab === "info" && canEdit && (
          <div className="border-t border-gray-200 px-6 py-4 flex justify-end space-x-3">
            <button
              onClick={onClose}
              className="px-4 py-2 text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
            >
              
            </button>
            <button
              onClick={handleInfoUpdate}
              disabled={loading}
              className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              {loading ? " ..." : ""}
            </button>
          </div>
        )}
      </div>
    </div>
  );
};
