import React, { useState, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { Check, X, Users, Building, Mail, Clock } from "lucide-react";
import { supabase } from "@/services/supabase";
import { useAuth } from "@/contexts/AuthContext";

interface InvitationData {
  id: string;
  email: string;
  role: string;
  status: string;
  expires_at: string;
  tenant_id: string;
  inviter_id: string;
  tenant?: {
    name: string;
  };
  inviter?: {
    name: string;
  };
}

export const InvitationAccept: React.FC = () => {
  const [searchParams] = useSearchParams();
  const { user } = useAuth();
  const navigate = useNavigate();
  const [invitation, setInvitation] = useState<InvitationData | null>(null);
  const [loading, setLoading] = useState(true);
  const [processing, setProcessing] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const token = searchParams.get("token");

  useEffect(() => {
    if (token) {
      loadInvitation();
    } else {
      setError("   .");
      setLoading(false);
    }
  }, [token]);

  const loadInvitation = async () => {
    try {
      const { data, error } = await supabase
        .from("team_invitations")
        .select(
          `
          id,
          email,
          role,
          status,
          expires_at,
          tenant_id,
          inviter_id
        `,
        )
        .eq("invitation_token", token)
        .eq("status", "pending")
        .single();

      if (error || !data) {
        setError("      .");
        return;
      }

      //  
      if (new Date(data.expires_at) < new Date()) {
        setError(" .");
        return;
      }

      //    (   )
      if (user?.email !== data.email) {
        setError("    .");
        return;
      }

      //     
      const [tenantResponse, inviterResponse] = await Promise.all([
        supabase
          .from("tenants")
          .select("name")
          .eq("id", data.tenant_id)
          .single(),
        supabase
          .from("user_profiles")
          .select("name")
          .eq("id", data.inviter_id)
          .single(),
      ]);

      const invitationWithDetails = {
        ...data,
        tenant: { name: tenantResponse.data?.name || "   " },
        inviter: { name: inviterResponse.data?.name || "   " },
      };

      setInvitation(invitationWithDetails);
    } catch (error) {
      console.error("   :", error);
      setError("   .");
    } finally {
      setLoading(false);
    }
  };

  const handleAccept = async () => {
    if (!invitation || !user) return;

    try {
      setProcessing(true);

      //   'accepted' 
      const { error: inviteError } = await supabase
        .from("team_invitations")
        .update({
          status: "accepted",
          updated_at: new Date().toISOString(),
        })
        .eq("id", invitation.id);

      if (inviteError) throw inviteError;

      //   tenant_id  

      const { error: profileError } = await supabase
        .from("user_profiles")
        .update({
          tenant_id: invitation.tenant_id,
          role: invitation.role,
          joined_at: new Date().toISOString(),
          status: "active",
        })
        .eq("id", user.id);

      if (profileError) throw profileError;

      //   
      await supabase.from("team_activity_logs").insert({
        tenant_id: invitation.tenant_id,
        user_id: user.id,
        action: "invitation_accepted",
        details: {
          invitation_id: invitation.id,
          role: invitation.role,
        },
      });

      alert("  !    .");

      //  
      setTimeout(() => {
        navigate("/");
      }, 1000);
    } catch (error: any) {
      console.error("  :", error);
      setError(error.message || "  .");
    } finally {
      setProcessing(false);
    }
  };

  const handleDecline = async () => {
    if (!invitation) return;

    if (!confirm("  ?")) return;

    try {
      setProcessing(true);

      //   'declined' 
      const { error } = await supabase
        .from("team_invitations")
        .update({
          status: "declined",
          updated_at: new Date().toISOString(),
        })
        .eq("id", invitation.id);

      if (error) throw error;

      alert(" .");
      navigate("/");
    } catch (error: any) {
      console.error("  :", error);
      setError(error.message || "  .");
    } finally {
      setProcessing(false);
    }
  };

  const getRoleLabel = (role: string) => {
    const labels = {
      owner: " ",
      admin: "",
      member: "",
      viewer: "",
    };
    return labels[role as keyof typeof labels] || role;
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500 mx-auto mb-4"></div>
          <p className="text-gray-600">   ...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="bg-white rounded-lg shadow-lg p-8 max-w-md w-full mx-4">
          <div className="text-center">
            <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <X className="w-8 h-8 text-red-600" />
            </div>
            <h2 className="text-xl font-semibold text-gray-900 mb-2">
               
            </h2>
            <p className="text-gray-600 mb-6">{error}</p>
            <button
              onClick={() => navigate("/")}
              className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition-colors"
            >
               
            </button>
          </div>
        </div>
      </div>
    );
  }

  if (!invitation) {
    return null;
  }

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center">
      <div className="bg-white rounded-lg shadow-lg p-8 max-w-md w-full mx-4">
        <div className="text-center mb-6">
          <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <Users className="w-8 h-8 text-blue-600" />
          </div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2"> </h2>
          <p className="text-gray-600">  </p>
        </div>

        <div className="space-y-4 mb-6">
          <div className="flex items-center space-x-3 p-3 bg-gray-50 rounded-lg">
            <Building className="w-5 h-5 text-gray-500" />
            <div>
              <p className="text-sm text-gray-600"></p>
              <p className="font-medium text-gray-900">
                {invitation.tenant?.name || "   "}
              </p>
            </div>
          </div>

          <div className="flex items-center space-x-3 p-3 bg-gray-50 rounded-lg">
            <Mail className="w-5 h-5 text-gray-500" />
            <div>
              <p className="text-sm text-gray-600"></p>
              <p className="font-medium text-gray-900">
                {invitation.inviter?.name || "   "}
              </p>
            </div>
          </div>

          <div className="flex items-center space-x-3 p-3 bg-gray-50 rounded-lg">
            <Users className="w-5 h-5 text-gray-500" />
            <div>
              <p className="text-sm text-gray-600"></p>
              <p className="font-medium text-gray-900">
                {getRoleLabel(invitation.role)}
              </p>
            </div>
          </div>

          <div className="flex items-center space-x-3 p-3 bg-gray-50 rounded-lg">
            <Clock className="w-5 h-5 text-gray-500" />
            <div>
              <p className="text-sm text-gray-600"></p>
              <p className="font-medium text-gray-900">
                {new Date(invitation.expires_at).toLocaleDateString("ko-KR")}
              </p>
            </div>
          </div>
        </div>

        <div className="flex space-x-3">
          <button
            onClick={handleDecline}
            disabled={processing}
            className="flex-1 bg-gray-100 text-gray-700 px-4 py-3 rounded-lg hover:bg-gray-200 transition-colors disabled:opacity-50 disabled:cursor-not-allowed font-medium"
          >
            
          </button>
          <button
            onClick={handleAccept}
            disabled={processing}
            className="flex-1 bg-blue-600 text-white px-4 py-3 rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed font-medium flex items-center justify-center space-x-2"
          >
            {processing ? (
              <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
            ) : (
              <>
                <Check className="w-5 h-5" />
                <span></span>
              </>
            )}
          </button>
        </div>

        <p className="text-xs text-gray-500 text-center mt-4">
                 .
        </p>
      </div>
    </div>
  );
};
