// ============================================================================
//   - Supabase Auth 
// ============================================================================

import React, {
  createContext,
  useContext,
  useEffect,
  useState,
  useMemo,
} from "react";
import { Session, AuthError } from "@supabase/supabase-js";
import { supabase } from "@/services/supabase";
import type { AuthUser, SignUpData, SignInData } from "@/types";
import { logger } from "@/utils/logger";

interface AuthContextType {
  user: AuthUser | null;
  session: Session | null;
  loading: boolean;
  signUp: (data: SignUpData) => Promise<any>;
  signIn: (data: SignInData) => Promise<any>;
  signOut: () => Promise<{ error: AuthError | null }>;
  resetPassword: (email: string) => Promise<{ error: AuthError | null }>;
  getCurrentUser: () => Promise<AuthUser | null>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
};

interface AuthProviderProps {
  children: React.ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<AuthUser | null>(null);
  const [session, setSession] = useState<Session | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    logger.info("   ");

    //    ( )
    const getSession = async () => {
      try {
        // 5  
        const timeoutPromise = new Promise((_, reject) =>
          setTimeout(() => reject(new Error("Session check timeout")), 5000),
        );

        const sessionPromise = supabase.auth.getSession();

        const {
          data: { session },
          error,
        } = (await Promise.race([sessionPromise, timeoutPromise])) as any;

        if (error) {
          logger.error("Error getting session:", error);
          setLoading(false);
          return;
        }

        setSession(session);
        if (session?.user) {
          try {
            //     ( )
            const profilePromise = supabase
              .from("user_profiles")
              .select("*")
              .eq("id", session.user.id)
              .single();

            const profileTimeoutPromise = new Promise((_, reject) =>
              setTimeout(
                () => reject(new Error("Profile fetch timeout")),
                3000,
              ),
            );

            const { data: profile } = (await Promise.race([
              profilePromise,
              profileTimeoutPromise,
            ])) as any;

            setUser({
              id: session.user.id,
              email: session.user.email!,
              name: profile?.name || session.user.user_metadata?.name || "",
              role: profile?.role || "owner",
              tenant_id: session.user.id, //  ID tenant_id 
              avatar_url: profile?.avatar_url || null,
              created_at: session.user.created_at,
              last_sign_in_at: session.user.last_sign_in_at || null,
            });
          } catch (profileError) {
            logger.error("Error fetching profile:", { error: profileError });
            //        
            setUser({
              id: session.user.id,
              email: session.user.email!,
              name: session.user.user_metadata?.name || session.user.email!,
              role: "owner",
              tenant_id: session.user.id,
              avatar_url: null,
              created_at: session.user.created_at,
              last_sign_in_at: session.user.last_sign_in_at || null,
            });
          }
        }
      } catch (error) {
        logger.error("Error in getSession:", { error });
      } finally {
        setLoading(false);
      }
    };

    getSession();

    //     ( )
    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange(async (event, session) => {
      logger.info(" Auth state changed:", {
        event,
        userId: session?.user?.id,
        userEmail: session?.user?.email,
      });

      setSession(session);

      if (session?.user) {
        try {
          //     ( )
          const profilePromise = supabase
            .from("user_profiles")
            .select("*")
            .eq("id", session.user.id)
            .single();

          const profileTimeoutPromise = new Promise((_, reject) =>
            setTimeout(() => reject(new Error("Profile fetch timeout")), 3000),
          );

          const { data: profile } = (await Promise.race([
            profilePromise,
            profileTimeoutPromise,
          ])) as any;

          setUser({
            id: session.user.id,
            email: session.user.email!,
            name: profile?.name || session.user.user_metadata?.name || "",
            role: profile?.role || "owner",
            tenant_id: session.user.id, //  ID tenant_id 
            avatar_url: profile?.avatar_url || null,
            created_at: session.user.created_at,
            last_sign_in_at: session.user.last_sign_in_at || null,
          });
        } catch (profileError) {
          logger.error("Error fetching profile in auth state change:", { 
            error: profileError 
          });
          //        
          setUser({
            id: session.user.id,
            email: session.user.email!,
            name: session.user.user_metadata?.name || session.user.email!,
            role: "owner",
            tenant_id: session.user.id,
            avatar_url: null,
            created_at: session.user.created_at,
            last_sign_in_at: session.user.last_sign_in_at || null,
          });
        }
      } else {
        setUser(null);
      }

      setLoading(false);
    });

    return () => subscription.unsubscribe();
  }, []);

  const signUp = async (data: SignUpData) => {
    try {
      const result = await supabase.auth.signUp({
        email: data.email,
        password: data.password,
        options: {
          data: {
            name: data.name,
            company: data.company,
          },
        },
      });

      //     ID tenant_id    
      if (result.data.user && !result.error) {
        try {
          logger.info("    :", { 
            userId: result.data.user.id 
          });

          //  ID tenant_id     
          const { error: profileError } = await supabase
            .from("user_profiles")
            .insert({
              id: result.data.user.id,
              email: data.email,
              name: data.name,
              role: "owner",
              tenant_id: result.data.user.id, //  ID = tenant_id  
            });

          if (profileError) {
            logger.error("Error creating user profile:", { 
              error: profileError 
            });
          } else {
            logger.info("     ");
          }
        } catch (error) {
          logger.error("Error in profile creation process:", { error });
        }
      }

      return result;
    } catch (error) {
      logger.error("Sign up error:", { error });
      return { user: null, error: error as AuthError };
    }
  };

  const signIn = async (data: SignInData) => {
    try {
      const result = await supabase.auth.signInWithPassword({
        email: data.email,
        password: data.password,
      });
      return result;
    } catch (error) {
      logger.error("Sign in error:", { error });
      return { user: null, error: error as AuthError };
    }
  };

  const signOut = async () => {
    try {
      const result = await supabase.auth.signOut();
      setUser(null);
      setSession(null);
      return result;
    } catch (error) {
      logger.error("Sign out error:", { error });
      return { error: error as AuthError };
    }
  };

  const resetPassword = async (email: string) => {
    try {
      const result = await supabase.auth.resetPasswordForEmail(email, {
        redirectTo: `${window.location.origin}/reset-password`,
      });
      return result;
    } catch (error) {
      logger.error("Reset password error:", { error });
      return { error: error as AuthError };
    }
  };

  const getCurrentUser = async (): Promise<AuthUser | null> => {
    try {
      logger.info(" getCurrentUser ");
      
      // 1.    
      if (user && !loading) {
        logger.info("   ", { userId: user.id });
        return user;
      }
      
      // 2. Supabase   (  )
      logger.info(" Supabase   ...");
      const { data: refreshData, error: refreshError } = await supabase.auth.refreshSession();
      
      if (refreshError) {
        logger.warn("   ,   :", { error: refreshError.message });
      } else if (refreshData.session) {
        logger.info("   ");
      }
      
      const { data: { user: supabaseUser }, error } = await supabase.auth.getUser();
      
      if (error || !supabaseUser) {
        logger.error(" getCurrentUser :", { error: error?.message });
        return null;
      }
      
      // 3.    AuthUser  
      try {
        const { data: profile } = await supabase
          .from("user_profiles")
          .select("*")
          .eq("id", supabaseUser.id)
          .single();

        const authUser: AuthUser = {
          id: supabaseUser.id,
          email: supabaseUser.email!,
          name: profile?.name || supabaseUser.user_metadata?.name || "",
          role: profile?.role || "owner",
          tenant_id: supabaseUser.id,
          avatar_url: profile?.avatar_url || null,
          created_at: supabaseUser.created_at,
          last_sign_in_at: supabaseUser.last_sign_in_at || null,
        };

        logger.info(" getCurrentUser :", { userId: authUser.id });
        return authUser;
      } catch (profileError) {
        logger.error("  ,    :", { error: profileError });
        
        const authUser: AuthUser = {
          id: supabaseUser.id,
          email: supabaseUser.email!,
          name: supabaseUser.user_metadata?.name || supabaseUser.email!,
          role: "owner",
          tenant_id: supabaseUser.id,
          avatar_url: null,
          created_at: supabaseUser.created_at,
          last_sign_in_at: supabaseUser.last_sign_in_at || null,
        };

        return authUser;
      }
    } catch (error) {
      logger.error("getCurrentUser  :", { error });
      return null;
    }
  };

  const value = useMemo(
    () => ({
      user,
      session,
      loading,
      signUp,
      signIn,
      signOut,
      resetPassword,
      getCurrentUser,
    }),
    [user, session, loading],
  );

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};
