import { useState, useEffect, useCallback } from "react";
import { useAuth } from "@/contexts/AuthContext";
import {
  getProperties,
  getPropertyStats,
  createProperty,
  updateProperty,
  deleteProperty,
} from "@/services";
import type {
  Property,
  SimplePropertyFilters,
  CreatePropertyData,
  UpdatePropertyData,
} from "@/types";

export const useProperties = (filters?: SimplePropertyFilters) => {
  const { user } = useAuth();
  const [properties, setProperties] = useState<Property[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchProperties = useCallback(async () => {
    if (!user?.id) {
      setLoading(false);
      setError(null);
      setProperties([]);
      return;
    }

    try {
      setLoading(true);
      setError(null);

      //     (  )
      if (!import.meta.env.VITE_SUPABASE_URL) {
        console.log(" :    ");
        const mockProperties: Property[] = [
          {
            id: "prop-1",
            tenant_id: user.id,
            created_by: user.id,
            title: "  ",
            type: "" as any,
            transaction_type: "" as any,
            status: "active" as any,
            price: 800000000,
            address: "  ",
            area: 84,
            floor: 15,
            total_floors: 25,
            rooms: 3,
            bathrooms: 2,
            description: " .",
            parking: true,
            elevator: true,
            images: [],
            view_count: 0,
            options: [],
            is_favorite: false,
            inquiry_count: 0,
            is_featured: false,
            is_urgent: false,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
          },
          {
            id: "prop-2", 
            tenant_id: user.id,
            created_by: user.id,
            title: " ",
            type: "" as any,
            transaction_type: "" as any,
            status: "active" as any,
            price: 300000000,
            address: "  ",
            area: 42,
            floor: 8,
            total_floors: 15,
            rooms: 1,
            bathrooms: 1,
            description: " .",
            parking: true,
            elevator: true,
            images: [],
            view_count: 0,
            options: [],
            is_favorite: false,
            inquiry_count: 0,
            is_featured: false,
            is_urgent: false,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
          }
        ];
        
        setProperties(mockProperties);
        setLoading(false);
        return;
      }

      //    API 
      const data = await getProperties(user.id, filters);
      setProperties((data as Property[]) || []);
    } catch (err) {
      console.error("   :", err);
      setError("    .");
      //     
      setProperties([]);
    } finally {
      setLoading(false);
    }
  }, [user?.id, filters]);

  useEffect(() => {
    fetchProperties();
  }, [fetchProperties]);

  const createNewProperty = useCallback(
    async (propertyData: CreatePropertyData) => {
      if (!user?.id) {
        throw new Error("   .   .");
      }

      try {
        const newProperty = await createProperty(
          propertyData,
          user.id,
          user.id,
        );

        setProperties((prev) => [newProperty, ...prev]);
        return newProperty;
      } catch (err) {
        console.error(" useProperties.createNewProperty :", err);
        console.error("  :", typeof err);
        console.error(
          "  :",
          err instanceof Error
            ? {
                name: err.name,
                message: err.message,
                stack: err.stack,
              }
            : err,
        );

        //     
        if (err instanceof Error) {
          throw err; //    
        } else {
          throw new Error("       .");
        }
      }
    },
    [user?.id],
  );

  const updateExistingProperty = useCallback(
    async (propertyId: string, propertyData: UpdatePropertyData) => {
      if (!user?.id) {
        throw new Error("  .");
      }

      try {
        const updatedProperty = await updateProperty(
          propertyId,
          propertyData,
          user.id,
        );
        setProperties((prev) =>
          prev
            .map((p) => (p.id === propertyId ? updatedProperty : p))
            .filter((p): p is Property => p !== null),
        );
        return updatedProperty;
      } catch (err) {
        console.error("Error updating property:", err);
        throw new Error("    .");
      }
    },
    [user?.id],
  );

  const deleteExistingProperty = useCallback(
    async (propertyId: string) => {
      if (!user?.id) {
        throw new Error("  .");
      }

      try {
        await deleteProperty(propertyId, user.id);
        setProperties((prev) => prev.filter((p) => p.id !== propertyId));
      } catch (err) {
        console.error("Error deleting property:", err);
        throw new Error("    .");
      }
    },
    [user?.id],
  );

  //     

  return {
    properties,
    loading,
    error,
    refreshProperties: fetchProperties,
    createProperty: createNewProperty,
    updateProperty: updateExistingProperty,
    deleteProperty: deleteExistingProperty,
    // updatePropertyStatus:     
  };
};

export const usePropertyStats = () => {
  const { user } = useAuth();
  const [stats, setStats] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchStats = async () => {
      if (!user?.id) return;

      try {
        setLoading(true);
        setError(null);
        
        //     (  )
        if (!import.meta.env.VITE_SUPABASE_URL) {
          console.log(" :    ");
          const mockStats = {
            total: 2,
            active: 2, //       
            reserved: 0,
            sold: 0, //   
            this_month: 2,
            total_users: 1,
            active_users: 1,
            by_transaction_type: {
              sale: 1,
              jeonse: 1,
              monthly: 0,
            },
          };
          setStats(mockStats);
          setLoading(false);
          return;
        }
        
        const data = await getPropertyStats(user.id);
        setStats(data);
      } catch (err) {
        console.error("Error fetching property stats:", err);
        //     fallback
        console.log("      ");
        const mockStats = {
          total: 2,
          active: 2, //       
          reserved: 0,
          sold: 0, //   
          this_month: 2,
          total_users: 1,
          active_users: 1,
          by_transaction_type: {
            sale: 1,
            jeonse: 1,
            monthly: 0,
          },
        };
        setStats(mockStats);
        setError(null);
      } finally {
        setLoading(false);
      }
    };

    fetchStats();
  }, [user?.id]);

  return { stats, loading, error };
};
