import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { Home, Plus, Users, Settings, X, BookOpen, Calendar } from "lucide-react";
import { Button } from "@/components/ui";
import { useTenant } from "@/contexts/TenantContext";
import { UserMenu } from "./UserMenu";
import { cn } from "@/utils/cn";

interface AppLayoutProps {
  children: React.ReactNode;
}

const AppLayout: React.FC<AppLayoutProps> = ({ children }) => {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const { tenant } = useTenant();
  const location = useLocation();

  const navigation = [
    { name: "", href: "/", icon: Home },
    { name: " ", href: "/properties", icon: Home },
    { name: " ", href: "/schedule", icon: Calendar },
    { name: " ", href: "/team", icon: Users },
    { name: "", href: "/settings", icon: Settings },
    { name: "", href: "/guide", icon: BookOpen },
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/*    */}
      {sidebarOpen && (
        <div
          className="fixed inset-0 z-40 bg-black bg-opacity-50 lg:hidden"
          onClick={() => setSidebarOpen(false)}
        />
      )}

      {/*   */}
      <div
        className={cn(
          "sidebar fixed inset-y-0 left-0 z-50 w-64 bg-white border-r border-gray-200 transition-transform duration-300 ease-in-out lg:translate-x-0 hidden lg:block",
          sidebarOpen ? "translate-x-0" : "-translate-x-full lg:translate-x-0",
        )}
      >
        <div className="flex flex-col h-full">
          {/*  */}
          <div className="flex items-center justify-between h-16 px-6 border-b border-gray-200">
            <div className="flex items-center space-x-3">
              <div
                className="w-8 h-8 rounded-lg flex items-center justify-center"
                style={{
                  background: `linear-gradient(135deg, ${tenant?.branding?.primary_color || "#3b82f6"}, ${tenant?.branding?.secondary_color || "#1d4ed8"})`,
                }}
              >
                <Home size={18} className="text-white" />
              </div>
              <div>
                <h1 className="text-lg font-bold">
                  {tenant?.name || " "}
                </h1>
                <p className="text-xs text-gray-500">{tenant?.plan} </p>
              </div>
            </div>
            <button
              onClick={() => setSidebarOpen(false)}
              className="lg:hidden p-1 text-gray-400 hover:text-gray-600"
            >
              <X size={20} />
            </button>
          </div>

          {/*    */}
          <div className="p-4">
            <Link to="/properties?create=true">
              <Button
                fullWidth
                variant="tenant"
                leftIcon={<Plus size={18} />}
                className="justify-center"
              >
                 
              </Button>
            </Link>
          </div>

          {/*  */}
          <nav className="flex-1 px-4 space-y-1">
            {navigation.map((item) => {
              const isCurrent = location.pathname === item.href;
              return (
                <Link
                  key={item.name}
                  to={item.href}
                  className={cn(
                    "nav-item flex items-center px-3 py-2 text-sm font-medium rounded-lg transition-colors",
                    isCurrent
                      ? "bg-primary-50 text-primary-700 border-primary-200"
                      : "text-gray-600 hover:text-gray-900 hover:bg-gray-100",
                  )}
                >
                  <item.icon className="w-5 h-5 mr-3" />
                  {item.name}
                </Link>
              );
            })}
          </nav>

          {/*   */}
          <div className="p-4 border-t border-gray-200">
            <div className="text-xs text-gray-500 space-y-2">
              <div>
                <p>: {tenant?.limits.max_properties || 0} </p>
                <p>: {tenant?.limits.max_users || 0} </p>
              </div>

              {/*   */}
              <div className="pt-2 border-t border-gray-300 space-y-1">
                <p>
                  <strong>:</strong> 
                </p>
                <p>
                  <strong>:</strong>  
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/*    -    */}
      <div className="lg:hidden fixed bottom-0 left-0 right-0 z-50 bg-white border-t border-gray-200 shadow-lg pb-safe">
        <div className="grid grid-cols-6 h-16 bg-white">
          {navigation.map((item) => {
            const isCurrent = location.pathname === item.href;
            return (
              <Link
                key={item.name}
                to={item.href}
                className={cn(
                  "nav-item flex flex-col items-center justify-center px-1 py-2 text-xs font-medium transition-all duration-200 touch-manipulation active:scale-95",
                  "min-h-[44px] relative",
                  isCurrent
                    ? "text-primary-600 bg-primary-50"
                    : "text-gray-500 hover:text-gray-700 hover:bg-gray-50",
                )}
              >
                {isCurrent && (
                  <div className="absolute top-0 left-1/2 w-8 h-1 bg-primary-600 rounded-b-full transform -translate-x-1/2" />
                )}
                <item.icon size={18} className="mb-1 flex-shrink-0" />
                <span className="truncate text-center leading-tight max-w-full">
                  {item.name.split(" ")[0]}
                </span>
              </Link>
            );
          })}
        </div>
      </div>

      {/*   */}
      <div className="lg:ml-64">
        {/*   */}
        <header className="bg-white border-b border-gray-200 h-16 flex items-center justify-between px-4 sm:px-6">
          <div className="flex items-center">
            <div className="flex items-center space-x-3 lg:hidden">
              <div
                className="w-8 h-8 rounded-lg flex items-center justify-center"
                style={{
                  background: `linear-gradient(135deg, ${tenant?.branding?.primary_color || "#3b82f6"}, ${tenant?.branding?.secondary_color || "#1d4ed8"})`,
                }}
              >
                <Home size={16} className="text-white" />
              </div>
              <h2 className="text-lg font-semibold">
                {tenant?.name || " "}
              </h2>
            </div>
            <h2 className="hidden lg:block text-lg font-semibold"></h2>
          </div>

          <div className="flex items-center space-x-2 sm:space-x-4">
            {/*     */}
            <Link to="/properties?create=true" className="lg:hidden">
              <Button
                size="sm"
                variant="tenant"
                leftIcon={<Plus size={16} />}
                className="px-3 py-2"
              >
                
              </Button>
            </Link>

            <UserMenu />
          </div>
        </header>

        {/*   */}
        <main className="p-4 sm:p-6 pb-24 lg:pb-6 min-h-screen">
          {children}
        </main>
      </div>
    </div>
  );
};

export { AppLayout };
